import { actionCreator } from '..';
import { filterData, transaction, txnList } from '../../types/transaction';
import {
  UPDATE_TRANSACTION_LIST,
  SET_TRANSACTION_DETAIL,
  RESET_TRANSACTION_LIST,
  UPDATE_TRANSACTION_LOADING,
  UPDATE_TRANSACTION_FILTERS,
} from '../actionTypes/transaction';

/**
 * Update transaction list
 * @param {Array<transaction>} payload
 * @returns {*}
 */
export const UpdateTransactionList: actionCreator<{
  transactions: transaction[];
  txnList: txnList;
  total: number;
}> = (payload) => ({
  type: UPDATE_TRANSACTION_LIST,
  payload,
});

/**
 * Update transaction list
 * @param {boolean} payload
 * @returns {*}
 */
export const UpdateTransactionLoading: actionCreator<boolean> = (payload) => ({
  type: UPDATE_TRANSACTION_LOADING,
  payload,
});

/**
 * Update transaction details
 * @param {transaction} payload
 * @returns {*}
 */
export const setTransactionDetail = (payload: transaction) => ({
  type: SET_TRANSACTION_DETAIL,
  payload,
});

/**
 * Reset transaction list
 * @returns {*}
 */
export const resetTransactionList = () => ({
  type: RESET_TRANSACTION_LIST,
});

/**
 * Update transaction filter
 * @param {filterData} payload
 * @returns {*}
 */
export const updateTransactionFilter = (payload: {data: filterData, tmpStates: any}) => ({
  type: UPDATE_TRANSACTION_FILTERS,
  payload,
});
