import { Action } from '..';
import {
  buyCryptoForm, buyQuote, cryptoAccount, sellCryptoForm, sellQuote, sendCryptoForm, sendCryptoGasFeesDetails, sendCryptoResponse, supportedCurrenciesResponse
} from '../../types/crypto';
import {
  UPDATE_CRYPTO_ACCOUNTS_LIST, UPDATE_SELECTED_CRYPTO_ACCOUNT, UPDATE_CRYPTO_BUY_QUOTE_DATA, UPDATE_CRYPTO_SELL_QUOTE_DATA, UPDATE_CRYPTO_BUY_FORM_DATA, UPDATE_CRYPTO_SELL_FORM_DATA, UPDATE_CRYPTO_SEND_FORM_DATA, UPDATE_CRYPTO_SEND_GAS_FEES_DATA, UPDATE_CRYPTO_SEND_RESPONSE_DATA, UPDATE_SUPPORTED_CURRENCIES
} from '../actionTypes/crypto';

interface cryptoStore {
    accounts: {
      data: cryptoAccount[],
      total: number
    };
    selectedAccount: cryptoAccount | null;
    buyQuoteData: buyQuote | null;
    buyFormData: buyCryptoForm | null;
    sellFormData: sellCryptoForm | null;
    sellQuoteData: sellQuote | null,
    sendFormData: sendCryptoForm | null,
    sendGasFeesData: sendCryptoGasFeesDetails | null,
    sendResponseData: sendCryptoResponse | null
    supportedCurrencies: supportedCurrenciesResponse
}

const initialData: cryptoStore = {
  accounts: {
    data: [],
    total: 0
  },
  selectedAccount: null,
  buyQuoteData: null,
  buyFormData: null,
  sellFormData: null,
  sellQuoteData: null,
  sendFormData: null,
  sendGasFeesData: null,
  sendResponseData: null,
  supportedCurrencies: {
    total: 0,
    data: []
  }
};

/**
 * account reducer
 * @param {cryptoStore} state
 * @param {Action} action
 * @returns {cryptoStore}
 */
const cryptoReducer = (
  state: cryptoStore = initialData,
  action: Action<string, any>,
) => {
  const copiedState = { ...state };

  switch (action.type) {
    case UPDATE_CRYPTO_ACCOUNTS_LIST:
      copiedState.accounts = action.payload;
      return copiedState;
    case UPDATE_SELECTED_CRYPTO_ACCOUNT:
      copiedState.selectedAccount = action.payload;
      return copiedState;
    case UPDATE_CRYPTO_BUY_QUOTE_DATA:
      copiedState.buyQuoteData = action.payload;
      return copiedState;
    case UPDATE_CRYPTO_BUY_FORM_DATA:
      copiedState.buyFormData = action.payload;
      return copiedState;
    case UPDATE_CRYPTO_SELL_FORM_DATA:
      copiedState.sellFormData = action.payload;
      return copiedState;
    case UPDATE_CRYPTO_SELL_QUOTE_DATA:
      copiedState.sellQuoteData = action.payload;
      return copiedState;
    case UPDATE_CRYPTO_SEND_FORM_DATA:
      copiedState.sendFormData = action.payload;
      return copiedState;
    case UPDATE_CRYPTO_SEND_GAS_FEES_DATA:
      copiedState.sendGasFeesData = action.payload;
      return copiedState;
    case UPDATE_CRYPTO_SEND_RESPONSE_DATA:
      copiedState.sendResponseData = action.payload;
      return copiedState;
    case UPDATE_SUPPORTED_CURRENCIES:
      copiedState.supportedCurrencies = action.payload;
      return copiedState;
    default:
      return copiedState;
  }
};

export default cryptoReducer;
