/* eslint-disable no-restricted-syntax */
/* eslint-disable class-methods-use-this */
import { changeLanguage } from 'i18next';
import localeInit, { addResource } from '../locale';
import store, { AsyncAppDispatch } from '../store';
import { updateSelectedAccountDetails, updateSelectedAccountType } from '../store/actionCreators/account';
import { updateSelectedBusiness } from '../store/actionCreators/business';
import { updateProgramConfig, updateSelectedLang, updateUiOptions } from '../store/actionCreators/core';
import { updateSelectedCryptoAccount } from '../store/actionCreators/crypto';
import { updatePersonDetails, updateUserAuthData } from '../store/actionCreators/user';
import { getAccountDetails } from '../store/middlewares/account';
import { getbusinessById } from '../store/middlewares/business';
import { fetchConfig, fetchProgramConfig } from '../store/middlewares/core';
import { getCryptoAccountDetails } from '../store/middlewares/crypto';
import { getPerson } from '../store/middlewares/user';
import { account } from '../types/account';
import { cryptoAccount } from '../types/crypto';
import { personDetails } from '../types/user';
import { apiErrorHandler } from '../utils/general';
import http from '../utils/http';
import { addMethod, callParentMethod } from '../utils/sdk';
import BasePage from './BasePage';

const listeners:{
  [key: string]: (...args: any[]) => any
} = {};

export const sdkMethod = (methodName?: string) => (target: any, memberName: string, descriptor:PropertyDescriptor) => {
  /**
   * add to listeners for later use
   */
  listeners[methodName || memberName] = descriptor.value;

  return descriptor;
};

/**
 * Base module
 */
export default abstract class BaseModule<T, S extends string = string> {
  /**
   * default start screen path
   */
  protected abstract url: string;

  /**
   * default start screen path
   */
   protected abstract pages: Record<S, any>;

  /**
   * default data
   */
  protected abstract defaultData: Omit<T, 'screenConfig'>;

  /**
   * redux dispatcher for dispatching the action
   */
  dispatch: AsyncAppDispatch = store.dispatch;

  /**
   * incomming data
   */
  protected data: T;

  /**
   * constructor
   * @param {*} data
   */
  constructor(data: T, onReady?: () => void) {
    this.data = data;

    // for (const key in listeners) {
    Object.keys(listeners).forEach((key) => {
      if (Object.prototype.hasOwnProperty.call(listeners, key)) {
        const element = listeners[key];
        addMethod(key, (...args): any => element.apply(this, args));
      }
    });

    if (onReady) {
      /**
       * wait for theme config
       */
      this.getThemeConfig()
        .then(({ config, person }: any) => {
          localeInit({
            en: {
              translation: {
                ...config.locale
              }
            }
          });

          setTimeout(() => {
            if (person.selectedLang && person.locales && person.locales[person.selectedLang]) {
              addResource(person.selectedLang, person.locales[person.selectedLang]);
            }
            changeLanguage(person.selectedLang || 'en');
            onReady();
          }, 0);
        });
    }
  }

  /**
   * Init method to start the flow
   * @returns
   */
  init(): Promise<string> {
    return new Promise((resolve) => {
      resolve(this.url);
    });
  }

  protected getThemeConfig() {
    return new Promise((resolve, reject) => {
      /**
       * fetch data from api
       */
      const fetchData = () => {
        this.getPersonDetails()
          .then((p) => {
            this.dispatch(fetchProgramConfig(p.programId))
              .then((programConfig) => {
                this.dispatch(fetchConfig())
                  .then((config: any) => {
                    const person = {
                      ...p,
                      selectedLang: 'en',
                      locales: {
                        en: store.getState().core.ui.locale,
                      }
                    };
                    resolve({
                      config,
                      person,
                      programConfig,
                    });
                  })
                  .catch(reject);
              }).catch(reject);
          }).catch(reject);
      };

      /**
       * check data is available in sdk
       */
      callParentMethod('getCurrentUserData', [])
        .then((d: any) => {
          if (d === null) {
            fetchData();
          } else {
            this.dispatch(updatePersonDetails(d.person));
            this.dispatch(updateSelectedLang(d.person.selectedLang || 'en'));
            this.dispatch(updateUiOptions(d.config));
            this.dispatch(updateProgramConfig(d.programConfig));
            resolve(d);
          }
        });
    });
  }

  // Common methods
  /**
   * Get person details by auth token
   * @returns {Promise<personDetails>} Person details
   */
  protected getPersonDetails(): Promise<personDetails> {
    return new Promise((resolve, reject) => {
      this.dispatch(getPerson())
        .then((p) => {
          resolve(p);
        }).catch(reject);
    });
  }

  /**
   * Get Account details by account id
   * @param {string} accountId
   * @param {'cash' | 'crypto'} accountType
   */
  protected checkAccount(accountId: string, accountType: 'cash' | 'crypto' = 'cash', getBusinessDetails = false):Promise<cryptoAccount | account> {
    return new Promise((resolve, reject) => {
      const fetchBusiness = (accountData: account) => {
        if (accountData && accountData.businessId !== '') {
          this.dispatch(getbusinessById(accountData.businessId))
            .then((b) => this.dispatch(updateSelectedBusiness(b)));
        }
      };

      const fetchData = () => {
        if (accountType === 'crypto') {
          this.dispatch(getCryptoAccountDetails(accountId))
            .then((a) => {
              this.dispatch(updateSelectedAccountType('crypto'));
              callParentMethod('setAccountDetails', [accountId, a]);
              resolve(a);
            }).catch((e) => {
              apiErrorHandler(e);
              reject(e);
            });
        } else {
          this.dispatch(getAccountDetails(accountId))
            .then((a) => {
              this.dispatch(updateSelectedAccountType('cash'));
              callParentMethod('setAccountDetails', [accountId, a]);
              if (getBusinessDetails) { fetchBusiness(a); }
              resolve(a);
            }).catch((e) => {
              apiErrorHandler(e);
              reject(e);
            });
        }
      };

      callParentMethod('getAccountDetails', [accountId])
        .then((d: any) => {
          if (d === null) {
            fetchData();
          } else {
            if (getBusinessDetails) { fetchBusiness(d); }
            if (accountType === 'crypto') {
              this.dispatch(updateSelectedAccountType('crypto'));
              this.dispatch(updateSelectedCryptoAccount(d));
            } else {
              this.dispatch(updateSelectedAccountType('cash'));
              this.dispatch(updateSelectedAccountDetails(d));
            }
            resolve(d);
          }
        });
    });
  }

  getPageDefaultParams(pageName: S) {
    const Page = this.pages[pageName];
    if (Page) {
      const pageObj:BasePage<any> = new Page((this.data as any)?.screenConfig || {});
      return pageObj.getDefaultData();
    }
    return {};
  }

  getDefaultParams() {
    return this.defaultData;
  }

  /**
   * TODO: Needs to remove once sdk token implemeted
   */
  @sdkMethod()
  public setAuthData(authToken: string) {
    return new Promise((resolve) => {
      if (typeof authToken === 'string') {
        this.dispatch(updateUserAuthData({
          accessToken: authToken,
        }));
        // Update http header
        http.defaults.headers.common.AUTHORIZATION = `Bearer ${authToken}`;
      }

      resolve(true);
    });
  }
}
