import BasePage, { pageInit } from '../BasePage';

export type AccountDetailsData = {
    showBack?: boolean;
    accountId?: string | null;
}

export default class AccountDetails extends BasePage<AccountDetailsData> {
  protected url = `/ao/account/details/${this.data.accountId}`;

  protected defaultData:AccountDetailsData = {
    showBack: true,
    accountId: null,
  };

  @pageInit()
  init() {
    return new Promise<string>((resolve, reject) => {
      if (this.data.accountId && this.data.accountId.length !== 0) {
        resolve(this.url);
      } else {
        reject(Error('INVALID_ACCOUNT'));
      }
    });
  }
}
