import BasePage, { pageInit } from '../BasePage';

export type ContactDetailsData = {
  contactId: string;
  showBack?: boolean;
}

export default class ContactDetails extends BasePage<ContactDetailsData> {
  protected url = `/contacts/details/${this.data.contactId}`;

  protected defaultData:ContactDetailsData = {
    contactId: '',
    showBack: true,
  };

  @pageInit()
  init(): Promise<string> {
    return new Promise((resolve, reject) => {
      if (this.data.contactId && this.data.contactId.length !== 0) {
        resolve(this.url);
      } else {
        reject(Error('INVALID_CONTACT'));
      }
    });
  }
}
