import React from 'react';
import {
  Box, Flex, Heading
} from '@chakra-ui/react';
import { useNavigate } from 'react-router-dom';
import style from './MiniBox.module.css';
import { useModuleData } from '../hooks/module';
import Icon from '../components/Icon';
import { useDrawer } from '../hooks/drawer';

type Props = React.FC<{
    varient?: 'box' | 'sidebar';
    heading?: string;
    showBack?: boolean;
    onBack?: () => void;
    bodyBg?: string;
    footer?: React.ReactNode;
    children: React.ReactNode;
    bodyClass?: Object;
}>

const MiniBox: Props = ({
  children, heading, footer, bodyBg, showBack = true, onBack, bodyClass, varient = 'sidebar'
}) => {
  const navigate = useNavigate();
  const drawerContext = useDrawer();
  const { adaptiveHeight = false } = useModuleData();

  return (
    <Flex className={varient === 'box' ? style.wrapperBox : style.wrapperSidebar}>
      <Box className={`${style.widgetContainer} ${heading ? style.widgetContainerWithHeader : ''} ${adaptiveHeight ? style.widgetContainerAdaptiveHeight : ''}`}>
        {heading ? (
          <Box className={style.navigationHeader}>
            {showBack
              ? (
                <Icon
                  icon="Arrow-big-left"
                  className={`${style.backIcon} cancel icon-customizable`}
                  size={16}
                  data-testid="back-arrow"
                  onClick={() => {
                    if (onBack) {
                      onBack();
                    } else if (varient === 'sidebar' && drawerContext) {
                      drawerContext.goBack();
                    } else {
                      navigate(-1);
                    }
                  }}
                />
              ) : null }
            <Box className={style.headingContainer}>
              <Heading data-testid="nav-title" variant="navTitle" className={style.heading}>
                {heading}
              </Heading>
            </Box>
          </Box>
        ) : null }
        <Flex flexDirection={footer ? 'column' : 'row'} className={`${style.widgetContainerBody} ${bodyClass}`} bg={bodyBg}>
          {children}
        </Flex>
        {footer ? (
          <Box className={`${style.footer} default-bg`}>
            {footer}
          </Box>
        ) : null }
      </Box>
    </Flex>

  );
};

export default MiniBox;
