import BasePage, { pageInit } from '../BasePage';

export type AccountDetailsData = {
    showBack?: boolean;
    businessId?: string | null;
}

export default class BusinessDetails extends BasePage<AccountDetailsData> {
  protected url = `/ao/account/business/details/${this.data.businessId}`;

  protected defaultData:AccountDetailsData = {
    showBack: true,
    businessId: null,
  };

  @pageInit()
  init() {
    return new Promise<string>((resolve, reject) => {
      if (this.data.businessId && this.data.businessId.length !== 0) {
        resolve(this.url);
      } else {
        reject(Error('INVALID_ACCOUNT'));
      }
    });
  }
}
