import { Action } from '..';
import { check } from '../../types/checkdeposit';
import { UPDATE_CHECK_DEPOSIT_DATA, UPDATE_CHECK_DEPOSIT_LIST } from '../actionTypes/checkpayment';

interface checkDepositStore {
  checkData: {
    frontImage: File | null
    backImage: File | null
    amount: string
  }
  checkList: {
    total: number
    data: check[]
  }
}

const initialTxn: checkDepositStore = {
  checkData: {
    frontImage: null,
    backImage: null,
    amount: ''
  },
  checkList: {
    total: 0,
    data: []
  }
};

/**
 * checkDeposit reducer
 * @param {checkDepositStore} state
 * @param {Action<string, any>} action
 * @returns {checkDepositStore}
 */
const checkDepositReducer = (
  state: checkDepositStore = initialTxn,
  action: Action<string, any>,
): checkDepositStore => {
  const copiedState: checkDepositStore = { ...state };
  switch (action.type) {
    case UPDATE_CHECK_DEPOSIT_DATA:
      copiedState.checkData = action.payload;
      return copiedState;
    case UPDATE_CHECK_DEPOSIT_LIST:
      copiedState.checkList = action.payload;
      return copiedState;
    default:
      return copiedState;
  }
};

export default checkDepositReducer;
