import { Action } from '..';
import { business, businessOwner } from '../../types/business';
import {
  UPDATE_BUSINESS_LIST, UPDATE_SELECTED_BUSINESS, UPDATE_OWNERS_LIST, UPDATE_SELECTED_BUSINESS_OWNER
} from '../actionTypes/business';

interface businessStore {
    businesses: business[];
    selectedBusiness: business | null;
    owners: businessOwner[];
    selectedBusinessOwner: businessOwner | null
}

const initialData: businessStore = {
  businesses: [],
  selectedBusiness: null,
  owners: [],
  selectedBusinessOwner: null
};

/**
 * account reducer
 * @param {businessStore} state
 * @param {Action} action
 * @returns {businessStore}
 */
const businessReducer = (
  state: businessStore = initialData,
  action: Action<string, any>,
) => {
  const copiedState = { ...state };

  switch (action.type) {
    case UPDATE_BUSINESS_LIST:
      copiedState.businesses = action.payload;
      return copiedState;
    case UPDATE_SELECTED_BUSINESS:
      copiedState.selectedBusiness = action.payload;
      return copiedState;
    case UPDATE_OWNERS_LIST:
      copiedState.owners = action.payload;
      return copiedState;
    case UPDATE_SELECTED_BUSINESS_OWNER:
      copiedState.selectedBusinessOwner = action.payload;
      return copiedState;
    default:
      return copiedState;
  }
};

export default businessReducer;
