import { actionCreator } from '..';
import {
  UPDATE_CORE_DATA,
  UPDATE_LOADING_STATE,
  UPDATE_MODULE_LOADING_STATE,
  UPDATE_UI_OPTIONS,
  UPDATE_PROGRAM_CONFIG,
  UPDATE_MODULE_DATA,
  UPDATE_DARK_MODE_FLAG,
  UPDATE_SELECTED_LANG,
} from '../actionTypes/core';
import { coreStore } from '../reducers/core';
/**
 * update core state
 * @param {*} payload
 * @returns {Action<string, any>}
 */
export const updateCoreData: actionCreator<coreStore['config']> = (payload) => ({
  type: UPDATE_CORE_DATA,
  payload,
});
/**
 * update app loader state
 * @param {boolean} payload
 * @returns {*}
 */
export const updateLoadingState: actionCreator<boolean> = (payload) => ({
  type: UPDATE_LOADING_STATE,
  payload,
});
/**
 * update module loader state
 * @param {boolean} payload
 * @returns {*}
 */
export const updateModuleLoadingState: actionCreator<boolean> = (payload) => ({
  type: UPDATE_MODULE_LOADING_STATE,
  payload,
});
/**
 * update app ui options
 * @param {boolean} payload
 * @returns {*}
 */
export const updateUiOptions: actionCreator<coreStore['ui']> = (payload) => ({
  type: UPDATE_UI_OPTIONS,
  payload,
});
/**
 * update program config data
 */
export const updateProgramConfig: actionCreator<coreStore['programConfig']> = (payload) => ({
  type: UPDATE_PROGRAM_CONFIG,
  payload
});

/**
 * update program config data
 */
export const updateModuleData: actionCreator<coreStore['moduleData']> = (payload) => ({
  type: UPDATE_MODULE_DATA,
  payload
});

/**
 * update dark mode flag
 */
export const updateDarkMode: actionCreator<Boolean> = (payload) => ({
  type: UPDATE_DARK_MODE_FLAG,
  payload
});

/**
 * update selected lang
 */
export const updateSelectedLang: actionCreator<string> = (payload) => ({
  type: UPDATE_SELECTED_LANG,
  payload
});
