import { Action } from '..';
import { paymentResponse } from '../../types/payment';
import { UPDATE_PAYMENT_RESPONSE } from '../actionTypes/payment';

interface transactionStore {
  paymentResponse: paymentResponse | null
}

const initialTxn: transactionStore = {
  paymentResponse: null
};

/**
 * Transaction reducer
 * @param {transactionStore} state
 * @param {Action<string, any>} action
 * @returns {transactionStore}
 */
const paymentReducer = (
  state: transactionStore = initialTxn,
  action: Action<string, any>,
): transactionStore => {
  const copiedState: transactionStore = JSON.parse(JSON.stringify({ ...state }));
  switch (action.type) {
    case UPDATE_PAYMENT_RESPONSE:
      copiedState.paymentResponse = action.payload;
      return copiedState;
    default:
      return copiedState;
  }
};

export default paymentReducer;
