import { actionCreator } from '..';
import { authData, personDetails } from '../../types/user';
import {
  UPDATE_USER_LOGIN_DATA,
  UPDATE_USER_AUTH_TOKEN_DATA,
  UPDATE_PERSON_DETAILS,
  UPDATE_KYC_DETAILS,
} from '../actionTypes/user';
import { userState } from '../reducers/user';

/** @typedef { import('../reducers/user').userDetails } userDetails */

/**
 * update user login details
 * @param {userDetails} payload
 * @returns {*}
 */
export const updateUserLoginData: actionCreator<
  userState['userDetails']
> = (payload) => ({
  type: UPDATE_USER_LOGIN_DATA,
  payload,
});
/**
 * update user login auth data
 * @param {*} payload
 * @returns {*}
 */
export const updateUserAuthData: actionCreator<
  authData
> = (payload) => ({
  type: UPDATE_USER_AUTH_TOKEN_DATA,
  payload,
});

/**
 * Update login person details
 * @param {*} payload
 * @returns {*}
 */
export const updatePersonDetails: actionCreator<personDetails> = (payload) => ({
  type: UPDATE_PERSON_DETAILS,
  payload,
});

/**
 * Update login person kyc details
 * @param {*} payload
 * @returns {*}
 */
export const updateKycDetails: actionCreator<
  personDetails['kyc']
> = (payload) => ({
  type: UPDATE_KYC_DETAILS,
  payload,
});
