import { apiErrorHandler } from '../../utils/general';
import BaseModule, { sdkMethod } from '../BaseModule';
import { getCard, listAllCardByAccount } from '../../store/middlewares/card';
import BasePage from '../BasePage';
import CardList, { CardListData } from './CardList';
import CardDetails, { CardDetailsData } from './CardDetails';
import CardActivate, { CardActiveData } from './CardActivate';
import SentCard, { SentCardData } from './SentCard';
import CardCreate, { CardCreateData } from './CardCreate';

/**
 * screens list
 */
type startScreen = 'cardList' | 'cardDetails' | 'cardActivate' | 'cardCreate' | 'sentCard'

/**
 * screen based config types
 */
type screenConfig<T> = T extends 'cardList' ? CardListData :
  (T extends 'cardDetails' ? CardDetailsData :
  (T extends 'cardActivate' ? CardActiveData :
  (T extends 'cardCreate' ? CardCreateData :
  (T extends 'sentCard' ? SentCardData :
  {
    [key: string]: number | string | boolean
  }))
  )
)
/**
 * module level data
 */
export type Data<T extends startScreen | unknown> = {
  startScreen: startScreen, // @default cardList
  accountId: string, // @default undefined
  accountType?: 'cash' | 'crypto', // @default cash
  screenConfig?: screenConfig<T> // @default {}
  gmapKey?: string
};

/**
 * Card module
 */
export default class Cards extends BaseModule<Data<unknown>, startScreen> {
  protected url = '/cards';

  defaultData: Data<unknown> = {
    startScreen: 'cardList',
    accountId: '',
    accountType: 'cash',
    gmapKey: ''
  };

  /**
   * page mapping
   */
  pages: Record<startScreen, any> = {
    cardList: CardList,
    cardDetails: CardDetails,
    cardActivate: CardActivate,
    cardCreate: CardCreate,
    sentCard: SentCard
  };

  /**
   * Initilize method which will return the url
   * @returns {Promise<string>}
   */
  init(): Promise<string> {
    return new Promise((resolve, reject) => {
      if (this.data.accountId && this.data.accountId.length !== 0) {
        this.checkAccount(this.data.accountId, this.data.accountType, true)
          .then(() => {
            const screen = this.data?.startScreen || 'cardList';
            const page: BasePage<startScreen> = new this.pages[screen](this.data.screenConfig || {});
            /**
             * Page initilize
             */
            page.init()
              .then((url) => {
                resolve(url);
              }).catch(reject);
          })
          .catch((e) => {
            apiErrorHandler(e);
            reject(Error('API_ERROR'));
          });
      } else {
        reject(Error('INVALID_ACCOUNT'));
      }
    });
  }

  @sdkMethod('getCards')
  getCards(accountId: string, limit:number = 20, offset: number = 0, query?: string) {
    return this.dispatch(listAllCardByAccount(accountId, limit, offset, query));
  }

  @sdkMethod('getCard')
  getCard(cardId: string) {
    return this.dispatch(getCard(cardId));
  }
}
