import { authData, personDetails } from '../../types/user';
import {
  UPDATE_KYC_DETAILS,
  UPDATE_PERSON_DETAILS,
  UPDATE_USER_AUTH_TOKEN_DATA,
  UPDATE_USER_LOGIN_DATA,
} from '../actionTypes/user';
import { Action } from '../index';

/**
 * user state object
 * @typedef {Object} userState
 * @property {string} bussinessDetails
 */
export type userState = {
  userDetails: {
    phone: string | null;
    idNumberLast4Required: boolean | null;
    otp: string | null;
    programCodeRequired: boolean | null;
  };
  authTokenDetails: authData;
  personDetails: personDetails | null;
};

const initialState: userState = {
  userDetails: {
    phone: null,
    idNumberLast4Required: null,
    otp: null,
    programCodeRequired: null,
  },
  authTokenDetails: {
    accessToken: null,
  },
  personDetails: null,
};

/**
 * user reducer
 * @param {userState} state
 * @param {*} action
 * @returns {userState} state
 */
const userReducer = (
  state: userState = initialState,
  action: Action<string, any>,
) => {
  const copiedState = { ...state };
  switch (action.type) {
    case UPDATE_USER_LOGIN_DATA:
      copiedState.userDetails = action.payload;
      return copiedState;
    case UPDATE_USER_AUTH_TOKEN_DATA:
      copiedState.authTokenDetails = action.payload;
      return copiedState;
    case UPDATE_PERSON_DETAILS:
      // console.log('UPDATE_PERSON_DETAILS', action.payload);
      copiedState.personDetails = action.payload;
      return copiedState;
    case UPDATE_KYC_DETAILS:
      if (copiedState.personDetails) {
        const copiedPersondDetails = { ...copiedState.personDetails };
        copiedPersondDetails.kyc = action.payload;
        copiedState.personDetails = copiedPersondDetails;
      }
      return copiedState;
    default:
      return copiedState;
  }
};

export default userReducer;
