import { Action } from '..';
import { filterData, transaction, txnList } from '../../types/transaction';
import {
  UPDATE_TRANSACTION_LIST,
  SET_TRANSACTION_DETAIL,
  UPDATE_TRANSACTION_LOADING,
  UPDATE_TRANSACTION_FILTERS,
} from '../actionTypes/transaction';

interface transactionStore {
  transactions: transaction[];
  transactionLoading: boolean;
  txnList: txnList;
  txnDetail: transaction | null;
  total: number;
  filters: {
    data: filterData,
    tmpStates: any,
  }
}

const initialTxn: transactionStore = {
  transactions: [],
  transactionLoading: false,
  txnList: {},
  txnDetail: null,
  total: 0,
  filters: {
    data: {},
    tmpStates: {
      transactionType: {
        credits: false,
        debits: false,
        cardTransactions: false,
      },
      amount: {
        min: '',
        max: ''
      },
      timePeriod: undefined,
      startDate: new Date(),
      endDate: new Date()
    }
  }
};

/**
 * Transaction reducer
 * @param {transactionStore} state
 * @param {Action<string, any>} action
 * @returns {transactionStore}
 */
const transactionReducer = (
  state: transactionStore = initialTxn,
  action: Action<string, any>,
): transactionStore => {
  const copiedState: transactionStore = JSON.parse(JSON.stringify({ ...state }));
  switch (action.type) {
    case UPDATE_TRANSACTION_LIST:
      copiedState.transactions = action.payload.transactions;
      copiedState.txnList = action.payload.txnList;
      copiedState.total = action.payload.total;
      return copiedState;
    case UPDATE_TRANSACTION_LOADING:
      copiedState.transactionLoading = action.payload;
      return copiedState;
    case SET_TRANSACTION_DETAIL:
      copiedState.txnDetail = action.payload;
      return copiedState;
    case UPDATE_TRANSACTION_FILTERS:
      copiedState.filters = action.payload;
      return copiedState;
    default:
      return copiedState;
  }
};

export default transactionReducer;
