import { Action } from '..';
import { themeConfig, themeStyle } from '../../types/theme';
import {
  UPDATE_CORE_DATA,
  UPDATE_LOADING_STATE,
  UPDATE_MODULE_LOADING_STATE,
  UPDATE_UI_OPTIONS,
  UPDATE_PROGRAM_CONFIG,
  UPDATE_MODULE_DATA,
  UPDATE_DARK_MODE_FLAG,
  UPDATE_SELECTED_LANG
} from '../actionTypes/core';

export interface coreStore {
  config: {
    apiBasePath: string;
    clientId: string;
    deviceId: string;
    origin: string;
    mode: 'sandbox' | 'live',
    recaptchaToken?: string;
    isDarkMode: boolean;
    showUI?: boolean;
    moduleName?: string;
    uniqueID?: string;
    uiOptions?: {
      loaderOverlayColor?: string
      loaderColor?: string
      loaderSize?: 'xs' | 'sm' | 'md' | 'lg' | 'xl'
    }
  };
  loadingStack: boolean[];
  isModuleLoading: boolean;
  ui: themeConfig;
  programConfig: {
    [key: string]: any
  } | null,
  moduleData: any;
  selectedLang: string;
}

export const initialUiOptions: coreStore['ui'] = {
  styles: {} as themeStyle,
  locale: {},
  modules: {}
};

const initialData: coreStore = {
  config: {
    apiBasePath: '',
    clientId: '',
    deviceId: '',
    origin: '',
    mode: 'sandbox',
    isDarkMode: false,
  },
  loadingStack: [],
  isModuleLoading: false,
  ui: initialUiOptions,
  programConfig: null,
  moduleData: null,
  selectedLang: 'en'
};
/**
 * reducer for core states
 * @param {coreStore} state
 * @param {Action<string, any>} action
 * @returns {coreStore}
 */
const coreReducer = (
  state: coreStore = initialData,
  action: Action<string, any>,
) => {
  const copiedState = { ...state };
  const copiedConfigState = { ...copiedState.config };
  const copiedLoadingStack = [...copiedState.loadingStack];

  switch (action.type) {
    case UPDATE_CORE_DATA:
      copiedState.config = action.payload;
      return copiedState;
    case UPDATE_LOADING_STATE:
      if (action.payload) {
        copiedLoadingStack.push(true);
      } else {
        copiedLoadingStack.pop();
      }
      copiedState.loadingStack = copiedLoadingStack;
      return copiedState;
    case UPDATE_MODULE_LOADING_STATE:
      copiedState.isModuleLoading = action.payload;
      return copiedState;
    case UPDATE_UI_OPTIONS:
      copiedState.ui = { ...copiedState.ui, ...action.payload };
      return copiedState;
    case UPDATE_PROGRAM_CONFIG:
      copiedState.programConfig = action.payload;
      return copiedState;
    case UPDATE_MODULE_DATA:
      copiedState.moduleData = action.payload;
      return copiedState;
    case UPDATE_DARK_MODE_FLAG:
      copiedConfigState.isDarkMode = action.payload;
      copiedState.config = copiedConfigState;
      return copiedState;
    case UPDATE_SELECTED_LANG:
      copiedState.selectedLang = action.payload;
      return copiedState;
    default:
      return copiedState;
  }
};

export default coreReducer;
