import React from 'react';
import {
  Modal, ModalOverlay, ModalContent, ModalBody, Spinner
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import { useAppSelector } from '../hooks/store';

type Props = {
    show?: boolean
    showModal?: boolean
}

const Loader:React.FC<Props> = ({ show = false, showModal = true }) => {
  const uiOptions = useAppSelector((store) => store.core.config.uiOptions, shallowEqual);
  const { isDarkMode } = useAppSelector((store) => store.core.config, shallowEqual);
  const loaderBg = isDarkMode ? 'rgba(0,0,0, 0.7)' : 'rgba(255, 255, 255, 0.7)';
  const loaderColr = isDarkMode ? '#fff' : '#000';
  const { loaderOverlayColor = loaderBg, loaderColor = loaderColr, loaderSize = 'lg' } = uiOptions || {};

  /**
   * loader with modal
   */
  if (showModal) {
    return (
      <Modal isCentered isOpen={show} onClose={() => {}}>
        <ModalOverlay bg={loaderOverlayColor} outline="none" _focus={{ boxShadow: 'none' }} />
        <ModalContent boxShadow="none" backgroundColor="transparent" display="flex" alignItems="center">
          <ModalBody>
            <Spinner color={loaderColor} size={loaderSize} />
          </ModalBody>
        </ModalContent>
      </Modal>
    );
  }
  return <Spinner color={loaderColor} size={loaderSize} />;
};

export default Loader;
