import { TypedUseSelectorHook, useDispatch, useSelector } from 'react-redux';
import type { RootState, AppDispatch, AsyncAppDispatch } from '../store';

/**
 * dispacth with attched type
 * @returns {Dispatch}
 */
export const useAppDispatch = () => useDispatch<AppDispatch>();
/**
 * async dispacth with attched type
 * @returns {Dispatch}
 */
export const useAppAsyncDispatch = () => useDispatch<AsyncAppDispatch>();

export const useAppSelector: TypedUseSelectorHook<RootState> = useSelector;
