import { apiErrorHandler } from '../../utils/general';
import BaseModule, { sdkMethod } from '../BaseModule';
import BasePage from '../BasePage';
import DashboardPage, { DashboardData } from './Dashboard';
import TransferMoney, { TransferMoneyDashboardData } from './TransferMoney';

type startScreen = 'dashboard' | 'transferMoney';

export type Data<T extends startScreen | unknown = unknown> = {
  accountId?: string,
  startScreen?: startScreen,
  accountType?: 'cash' | 'crypto',
  screenConfig?: T extends 'dashboard' ? DashboardData : TransferMoneyDashboardData
}

export default class Dashboard extends BaseModule<Data> {
  protected url = '/dashboard';

  defaultData: Data = {
    accountId: '',
    startScreen: 'dashboard'
  };

  /**
   * page mapping
   */
  pages: Record<startScreen, any> = {
    dashboard: DashboardPage,
    transferMoney: TransferMoney,
  };

  init(): Promise<string> {
    return new Promise((resolve, reject) => {
      if (this.data.accountId && this.data.accountId.length !== 0) {
        this.checkAccount(this.data.accountId, this.data.accountType, true)
          .then(() => {
            const screen = this.data?.startScreen || 'dashboard';
            const page: BasePage<startScreen> = new this.pages[screen](this.data.screenConfig || {});
            /**
             * Page initilize
             */
            page.init()
              .then((url) => {
                resolve(url);
              }).catch(reject);
          })
          .catch((e) => {
            apiErrorHandler(e);
            reject(Error('API_ERROR'));
          });
      } else {
        reject(Error('INVALID_ACCOUNT'));
      }
    });
  }

  /**
   * get dashboard ui config data
   * @param {string} keyName
   * @returns {Promise<any>}
   */
  // eslint-disable-next-line class-methods-use-this
  @sdkMethod('getUIConfig')
  getUIConfig(keyName: string) :Promise<any> {
    return new Promise((resolve) => {
      this.getThemeConfig().then((v: any) => {
        const configData = v.config;
        if (configData?.modules?.dashboard && configData.modules.dashboard[keyName] !== undefined) {
          resolve(configData.modules.dashboard[keyName]);
        } else {
          resolve(undefined);
        }
      }).catch(() => {
        resolve(undefined);
      });
    });
  }
}
