import BaseModule, { sdkMethod } from '../BaseModule';
import { listAllAccounts, getAccountDetails, fetchStatements } from '../../store/middlewares/account';
import { listAllCryptoAccounts, getCryptoAccountDetails } from '../../store/middlewares/crypto';
import { account, accountStatement } from '../../types/account';
import { cryptoAccount } from '../../types/crypto';
import AccountList, { AccountListData } from './AccountList';
import AccountDetails, { AccountDetailsData } from './AccountDetails';
import BasePage from '../BasePage';
import AccountStatements, { AccountStatementsData } from './AccountStatements';
import { callParentMethod } from '../../utils/sdk';
import BusinessDetails from './BusinessDetails';
import PersonDetails from './PersonDetails';

export type startScreen = 'accountList' | 'accountDetails' | 'accountStatements' | 'businessDetails' | 'personDetails';

export type Data<T extends startScreen | unknown = unknown> = {
  startScreen: startScreen
  accountType?: 'cash' | 'crypto',
  gmapKey?: string,
  screenConfig?: T extends 'accountList' ? AccountListData : (
    T extends 'accountDetails' ? AccountDetailsData : AccountStatementsData
  )
}

export default class WalletList extends BaseModule<Data> {
  protected url = '/ao/account/list';

  defaultData: Data = {
    startScreen: 'accountList',
    accountType: 'cash'
  };

  /**
   * page mapping
   */
  pages: Record<startScreen, any> = {
    accountList: AccountList,
    accountDetails: AccountDetails,
    accountStatements: AccountStatements,
    businessDetails: BusinessDetails,
    personDetails: PersonDetails
  };

  init(): Promise<string> {
    return new Promise((resolve, reject) => {
      const screen = this.data?.startScreen || 'accountList';
      const page: BasePage<startScreen> = new this.pages[screen](this.data.screenConfig || {});
      /**
           * Page initilize
           */
      page.init()
        .then((url) => {
          console.log(url);
          resolve(url);
        }).catch(reject);
    });
  }

  /**
   * get cash accounts list
   * @param {number} limit
   * @param {number} offset
   * @param {string} businessId
   * @param {'card' | 'personalChecking' | 'businessChecking'} subType
   * @returns {Promise<{data: account[], total: number}>}
   */
  @sdkMethod('getCashAccounts')
  getCashAccounts(limit:number = 20, offset: number = 0, businessId?: string, subType?: string) :Promise<{data: account[], total: number}> {
    return this.dispatch(listAllAccounts(businessId, subType, limit, offset));
  }

  /**
   * get crypto accounts list
   * @param {number} limit
   * @param {number} offset
   * @param {string} businessId
   * @param {'card' | 'personalChecking' | 'businessChecking'} subType
   * @returns {Promise<{data: cryptoAccount[], total: number}>}
   */
  @sdkMethod('getCryptoAccounts')
  getCryptoAccounts(limit:number = 20, offset: number = 0, businessId?: string, subType?: string) :Promise<{data: cryptoAccount[], total: number}> {
    return this.dispatch(listAllCryptoAccounts(businessId, subType, limit, offset));
  }

  /**
   * get cash accounts details by id
   * @param {string} accountId
   * @returns {Promise<account>}
   */
  @sdkMethod('getCashAccountDetails')
  getCashAccountDetails(accountId: string, storeItInCache = true): Promise<account> {
    return new Promise((resolve, reject) => {
      this.dispatch(getAccountDetails(accountId))
        .then((d) => {
          if (storeItInCache) {
            callParentMethod('setAccountDetails', [accountId, d]);
          }
          resolve(d);
        }).catch(reject);
    });
  }

  /**
   * get crypto accounts details by id
   * @param {string} accountId
   * @returns {Promise<cryptoAccount>}
   */
  @sdkMethod('getCryptoAccountDetails')
  getCryptoAccountDetails(accountId: string, storeItInCache = true): Promise<cryptoAccount> {
    return new Promise((resolve, reject) => {
      this.dispatch(getCryptoAccountDetails(accountId))
        .then((d) => {
          if (storeItInCache) {
            callParentMethod('setAccountDetails', [accountId, d]);
          }
          resolve(d);
        }).catch(reject);
    });
  }

  /**
   * get cash account statement by account id
   * @param {string} accountId
   * @returns {Promise<{total: number, data: accountStatement[]}>}
   */
  @sdkMethod('getAccountStatements')
  getAccountStatements(accountId: string): Promise<{total: number, data: accountStatement[]}> {
    return this.dispatch(fetchStatements(accountId));
  }
}
