import { Country } from 'react-phone-number-input';
import { updateSelectedBusiness } from '../../store/actionCreators/business';
import { getAllOwners, getbusinessById, listAllbusiness } from '../../store/middlewares/business';
import BaseModule, { sdkMethod } from '../BaseModule';
import BasePage from '../BasePage';
import BusinessDetailsForm, { BusinessDetailsFormData } from './BusinessDetailsForm';
import OwnersList, { OwnersListData } from './OwnersList';

type startScreen = 'ownersList' | 'businessDetailsForm';

export type Data<T extends startScreen | unknown = unknown> = {
  startScreen?: startScreen,
  supportedCountries?: Country[]
  screenConfig?: T extends 'ownersList' ? OwnersListData : BusinessDetailsFormData
}

export default class Kyb extends BaseModule<Data> {
  protected url = '/ao/kyb/businessDetails';

  defaultData: Data<unknown> = {
    startScreen: 'businessDetailsForm',
    supportedCountries: ['US']
  };

  /**
   * page mapping
   */
  pages: Record<startScreen, any> = {
    ownersList: OwnersList,
    businessDetailsForm: BusinessDetailsForm,
  };

  init(): Promise<string> {
    return new Promise((resolve, reject) => {
      const screen = this.data?.startScreen || 'businessDetailsForm';
      console.log(screen, this.data);
      const page: BasePage<startScreen> = new this.pages[screen](this.data.screenConfig || {});
      const businessId = this.data?.screenConfig?.businessId;

      const pageInit = () => {
        /**
         * Page initilize
         */
        page.init()
          .then((url) => {
            resolve(url);
          }).catch(reject);
      };

      if (businessId && businessId.length !== 0) {
        this.getBusinessDetails(businessId)
          .then((data) => {
            this.dispatch(updateSelectedBusiness(data));
            if (data.kyb.status === 'notStarted' && this.data.startScreen === 'businessDetailsForm') {
              pageInit();
            } else if (data.kyb.status === 'approved') {
              // ownership details are not required for this types
              if (['soleProprietor', 'singleMemberLLC'].includes(data.entityType)) {
                resolve('/ao/kyb/approved');
              } else if (!this.data.startScreen || this.data.startScreen === 'businessDetailsForm') {
                // business details page with lesser than 100% totalOwnership
                this.dispatch(getAllOwners(data.id))
                  .then(({ data: owners }) => {
                    // eslint-disable-next-line radix
                    const totalOwnership = owners.reduce((total, o) => total + parseInt(o.ownership), 0);
                    if (totalOwnership !== 100) {
                      resolve('/ao/kyb/ownership');
                    } else {
                      resolve('/ao/kyb/approved');
                    }
                  });
              } else {
                // owners details page with approved status
                pageInit();
              }
            } else if (data.kyb.status === 'declined') {
              resolve('/ao/kyb/declined');
            } else {
              resolve('/ao/kyb/review');
            }
          });
      } else {
        pageInit();
      }
    });
  }

  @sdkMethod('getBusinessList')
  getBusinessList(limit?: number, offset?: number) {
    return this.dispatch(listAllbusiness(limit, offset));
  }

  @sdkMethod('getBusinessDetails')
  getBusinessDetails(id: string) {
    return this.dispatch(getbusinessById(id));
  }

  @sdkMethod('getAllOwners')
  getAllOwners(businessId: string) {
    return this.dispatch(getAllOwners(businessId));
  }
}
