/* eslint-disable camelcase */
import React, { useEffect, useState } from 'react';
import { shallowEqual } from 'react-redux';
import AppRouter from './AppRouter';
import { useAppSelector } from './hooks/store';

import './App.css';
import http from './utils/http';

function App() {
  const [isLoaded, setLoadingState] = useState(false);
  const coreConfig = useAppSelector((store) => store.core.config, shallowEqual);

  useEffect(() => {
    http.defaults.baseURL = coreConfig.apiBasePath;
    http.defaults.headers.common['SD-Device-ID'] = coreConfig.deviceId;
    // http.defaults.headers['SD-Device-ID'] = coreConfig.deviceId;

    setLoadingState(true);
  }, [coreConfig]);

  if (!isLoaded) {
    return null;
  }

  return (
    <AppRouter />
  );
}

export default App;
