import { COMMON } from '../../const/errors';
import store from '../../store';
import { getKycData, idvStatus } from '../../store/middlewares/user';
import { findNextScreen } from '../../utils/general';
import BaseModule, { sdkMethod } from '../BaseModule';

export type Data = {
  gmapKey?: string
  screenConfig?: {
    showBack: boolean
  }
}

export default class Kyc extends BaseModule<Data> {
  protected url = '/ao/kyc/personalDetails';

  pages = {};

  defaultData: Data = {
    gmapKey: '',
    screenConfig: {
      showBack: true
    }
  };

  constructor(d: Data, onReady: () => void) {
    super(d, onReady);
    this.data = {
      ...d,
      ...{
        screenConfig: {
          showBack: true
        }
      }
    };
  }

  // eslint-disable-next-line class-methods-use-this
  init(): Promise<string> {
    return new Promise((resolve, reject) => {
      const person = store.getState().user.personDetails;
      if (person?.kyc) {
        const nextScreen = findNextScreen(person?.kyc);
        resolve(nextScreen);
      } else {
        reject(Error(COMMON.INVALID_AUTH_TOKEN.TITLE));
      }
    });
  }

  @sdkMethod('getPersonDetails')
  getPerson() {
    /** get person details  */
    return this.getPersonDetails();
  }

  @sdkMethod('getIntialData')
  getIntialData() {
    return this.getThemeConfig();
  }

  @sdkMethod('getKycDetails')
  getKycDetails(personId: string) {
    return this.dispatch(getKycData(personId));
  }

  @sdkMethod('getIdvStatus')
  getIdvStatus(personId: string) {
    return this.dispatch(idvStatus(personId));
  }
}
