import React from 'react';
import { Route, Routes } from 'react-router-dom';
import DashboardLayout from '../../layout/Dashboard';

const TransferMoney = React.lazy(() => import('./TransferMoney/TransferMoney'));

/**
 * Fund Routes
 * @returns {JSX.Element}
 */
function FundRouter() {
  return (
    <Routes>
      <Route path="" element={<DashboardLayout><TransferMoney /></DashboardLayout>} />
    </Routes>
  );
}

export default FundRouter;
