import { updateSelectedCard } from '../../store/actionCreators/card';
import { getCard } from '../../store/middlewares/card';
import BasePage, { pageInit } from '../BasePage';

export type CardActiveData = {
  cardId: string;
  showBack?: boolean;
}

export default class CardActivate extends BasePage<CardActiveData> {
  protected url = '/cards/activate';

  protected defaultData = {
    cardId: '',
    showBack: true,
  };

  @pageInit()
  init() {
    return new Promise<string>((resolve, reject) => {
      if (this.data.cardId && this.data.cardId.length !== 0) {
        this.getCard(this.data.cardId)
          .then((c) => {
            this.dispatch(updateSelectedCard(c));
            resolve('/cards/activate');
          }).catch(() => {
            reject(Error('INVALID_CARD'));
          });
      } else {
        reject(new Error('INVALID_CARD'));
      }
    });
  }

  getCard(cardId: string) {
    return this.dispatch(getCard(cardId));
  }
}
