// sdk communication related methods

import MESSAGES from '../enums/messages';
import store from '../store';
// import { COMMON as COMMON_ERROR } from '../const/errors';

export type PostData = any;

type cb = (data: any) => void;

const listeners:{
  [key: string]: cb[]
} = {};

type Message = {TITLE: MESSAGES, DATA: unknown};

// eslint-disable-next-line consistent-return
export function sendMessage<T extends Message | string, R extends(T extends Message ? (
  T['DATA']
) : unknown)>(...args: R extends undefined ? [message: T, data?: R, uID?: number | string, type?:'message' | 'error'] : (
  T extends string ? [message: T, data?: R, uID?: number | string, type?:'message' | 'error'] : [message: T, data: R, uID?: number | string, type?:'message' | 'error'])) {
  const [message, data, uID, type] = args;

  const code = typeof message === 'string' ? message : message.TITLE;

  const { origin, uniqueID } = store.getState().core.config;

  window.parent.postMessage({
    type: type || 'message',
    data: data || {},
    code,
    uID,
    SOLID_UNIQUE_ID: uniqueID // SOLID_UNIQUE_ID will use for compare the source and target iframe.
  }, origin || '*');

  if (uID) {
    return new Promise((resolve) => {
      listeners[`${uID}`] = [(d) => {
        resolve(d);
      }];
    });
  }
}

/**
 * append listeners
 * @param {string} eventName
 * @param {cb} callback
 */
export const addListener = (eventName: string, callback: cb) => {
  if (!listeners[eventName]) {
    listeners[eventName] = [];
  }

  listeners[eventName].push(callback);
};

window.addEventListener('message', (e) => {
  if (listeners[e.data?.event]) {
    listeners[e.data?.event].forEach((cb) => {
      cb(e.data?.data);
    });
  }
});

/**
 * add a module level method
 * @param methodName
 * @param cb
 */
export const addMethod = (methodName: string, cb: (...args: any[]) => Promise<any>) => {
  addListener('methodCall', (e) => {
    if (e.name === methodName) {
      cb(...e.args).then((p) => {
        sendMessage('methodCallResponse', p, e.uID);
      }).catch((er) => {
        sendMessage('methodCallResponse', {
          data: {
            type: 'error',
            error: er
          }
        }, e.uID);
        // sendMessage(COMMON_ERROR.UNKNOWN_ERROR, { error: er }, e.uID, 'error');
      });
    }
  });
};

/**
 * Call sdk methods
 * @param name
 * @param args
 * @param timeout 30s
 * @returns
 */
export const callParentMethod = (name: string, args: any[], timeout = 30000) => new Promise((resolve, reject) => {
  const uID = `${Math.floor(Date.now())}-${name}`;
  sendMessage('methodCall', {
    name,
    args,
  }, uID);

  const timer = setTimeout(() => {
    reject(new Error('TIMEOUT'));
  }, timeout);

  addListener('methodCallResponse', (d) => {
    if (uID === d.uID) {
      clearTimeout(timer);
      if (d?.data?.type === 'error') {
        reject(d.data?.error);
      } else {
        resolve(d.data);
      }
    }
  });
});
