import BasePage, { pageInit } from '../BasePage';
import store from '../../store';
import { updateSetupWalletOptions } from '../../store/actionCreators/account';

export type SetupData = {
  showBack?: boolean;
  accountType?: 'cash' | 'crypto' | 'card',
  businessId?: string | null,
  evolveTerms?: string
  platformTerms?: string
  walletTerms?: string
}

export default class Setup extends BasePage<SetupData> {
  protected url = '/ao/account/setup';

  protected defaultData:SetupData = {
    showBack: true,
    accountType: 'cash',
    businessId: null,
    evolveTerms: 'https://www.solidfi.com/',
    platformTerms: 'https://www.solidfi.com/',
    walletTerms: 'https://www.solidfi.com/'
  };

  @pageInit()
  init(): Promise<string> {
    const person = store.getState().user.personDetails;
    return new Promise((resolve, reject) => {
      this.dispatch(updateSetupWalletOptions({
        accountType: this.data.accountType || 'cash',
        businessId: this.data.businessId || null
      }));
      switch (person?.kyc.status) {
        case 'notStarted':
          reject(Error('KYC_NOT_STARTED'));
          break;
        case 'approved':
          resolve(this.url);
          break;
        case 'declined':
          reject(Error('KYC_DECLINED'));
          break;
        default:
          reject(Error('KYC_INREVIEW'));
      }
    });
  }
}
