import { Center, ChakraProvider } from '@chakra-ui/react';
import React from 'react';
import { shallowEqual } from 'react-redux';
import {
  BrowserRouter as Router,
  Routes,
  Route,
} from 'react-router-dom';
import Loader from './components/Loader';
import ProtectedRoute from './components/ProtectedRoute';
import { useTheme } from './hooks/app';
import { useAppSelector } from './hooks/store';
import FundRouter from './views/fund/Index';
import LoginCallback from './views/LoginCallback';
import Welcome from './views/Welcome';

const AuthRouter = React.lazy(() => import('./views/auth/Index'));
const AoRouter = React.lazy(() => import('./views/ao/Index'));
const TransactionsRouter = React.lazy(() => import('./views/transactions/Index'));
const CardsRouter = React.lazy(() => import('./views/cards/Index'));
const PullFundsRouter = React.lazy(() => import('./views/pullfunds/Index'));
const ContactsRouter = React.lazy(() => import('./views/contacts/Index'));
const PaymentsRouter = React.lazy(() => import('./views/payments/Index'));
const CryptoRouter = React.lazy(() => import('./views/crypto/Index'));
const DashboardRouter = React.lazy(() => import('./views/dashboard/Index'));

const IdvCallback = React.lazy(() => import('./views/ao/Kyc/IdvCallback/IdvCallback'));

/**
 * Routes childre component with logout logic
 * @returns {JSX.Element}
 */
function RoutesList() {
  const { ui: configData, loadingStack } = useAppSelector((store) => store.core, shallowEqual);
  const isModuleLoading = useAppSelector((store) => store.core.isModuleLoading);

  const theme = useTheme(configData);
  return (
    <ChakraProvider theme={theme}>
      {
    isModuleLoading ? (
      <div className="moduleLoaderSection">
        <Center>
          <Loader show />
        </Center>
      </div>
    )
      : (
        <>
          <Routes>

            <Route
              path="auth/*"
              element={(
                <React.Suspense fallback={<>...</>}>
                  <AuthRouter />
                </React.Suspense>
          )}
            />

            <Route
              path="ao/*"
              element={(

                <React.Suspense fallback={<Loader show />}>
                  <ProtectedRoute>
                    <AoRouter />
                  </ProtectedRoute>
                </React.Suspense>
            )}
            />

            <Route
              path="transactions/*"
              element={(
                <React.Suspense fallback={<Loader show />}>
                  <ProtectedRoute>
                    <TransactionsRouter />
                  </ProtectedRoute>
                </React.Suspense>
            )}
            />

            <Route
              path="cards/*"
              element={(
                <React.Suspense fallback={<Loader show />}>
                  <ProtectedRoute>
                    <CardsRouter />
                  </ProtectedRoute>
                </React.Suspense>
            )}
            />

            <Route
              path="pullfunds/*"
              element={(
                <React.Suspense fallback={<Loader show />}>
                  <ProtectedRoute>
                    <PullFundsRouter />
                  </ProtectedRoute>
                </React.Suspense>
            )}
            />

            <Route
              path="contacts/*"
              element={(
                <React.Suspense fallback={<Loader show />}>
                  <ProtectedRoute>
                    <ContactsRouter />
                  </ProtectedRoute>
                </React.Suspense>
            )}
            />

            <Route
              path="payments/*"
              element={(
                <React.Suspense fallback={<Loader show />}>
                  <ProtectedRoute>
                    <PaymentsRouter />
                  </ProtectedRoute>
                </React.Suspense>
            )}
            />

            <Route
              path="crypto/*"
              element={(
                <React.Suspense fallback={<Loader show />}>
                  <ProtectedRoute>
                    <CryptoRouter />
                  </ProtectedRoute>
                </React.Suspense>
            )}
            />

            <Route
              path="dashboard/*"
              element={(
                <React.Suspense fallback={<Loader show />}>
                  <ProtectedRoute>
                    <DashboardRouter />
                  </ProtectedRoute>
                </React.Suspense>
            )}
            />

            <Route
              path="fund/*"
              element={(
                <React.Suspense fallback={<Loader show />}>
                  <ProtectedRoute>
                    <FundRouter />
                  </ProtectedRoute>
                </React.Suspense>
            )}
            />

            <Route path="idvCallback" element={<IdvCallback />} />

            <Route path="/login-callback" element={<LoginCallback />} />
            <Route path="/" element={<Welcome />} />
          </Routes>

          <Loader show={loadingStack.length !== 0} />
        </>
      )
    }
    </ChakraProvider>
  );
}
const RoutesListMemo = React.memo(RoutesList);

/**
 * Router wrapper
 * @param {Props} param0
 * @returns {JSX.Element}
 */
function AppRouter() {
  const { config } = useAppSelector((state) => state.core, shallowEqual);
  return (
    <div className={`solid-theme-wrapper theme-${config.isDarkMode ? 'dark' : 'light'}`}>
      <Router>
        <RoutesListMemo />
      </Router>

    </div>
  );
}

export default React.memo(AppRouter);
