import BasePage, { pageInit } from '../BasePage';

export type OwnersListData = {
    showBack?: boolean;
    businessId: string;
  }

export default class OwnersList extends BasePage<OwnersListData> {
  protected url = '/ao/kyb/ownership';

  protected defaultData:OwnersListData = {
    showBack: true,
    businessId: '',
  };

  @pageInit()
  init() {
    return new Promise<string>((resolve, reject) => {
      if (this.data.businessId && this.data.businessId.length !== 0) {
        resolve(this.url);
      } else {
        reject(Error('INVALID_BUSINESS'));
      }
    });
  }
}
