import axios from 'axios';
import { AsyncAppDispatch, RootState } from '..';
import { loading } from '../../utils/general';
import http from '../../utils/http';
import { updateProgramConfig, updateUiOptions } from '../actionCreators/core';

const json = require('./sample-config.json');
/**
 * fetch config json
 * @returns {Promise}
 */
export const fetchConfig = () => (dispatch:AsyncAppDispatch, getState: () => RootState) => new Promise((resolve) => {
  loading(true);
  const state = getState();
  const themeURL = state.core.programConfig?.brand?.sdkTheme?.themeURL;
  /* */
  axios
    .get(themeURL)
    // .then(() => {
    .then(({ data }) => {
      dispatch(updateUiOptions(data));
      resolve(data);
    })
    .catch(() => {
      dispatch(updateUiOptions(json));
      resolve(json);
    })
    .finally(() => {
      loading(false);
    });

  /* */
  //
});

/**
 * fetch locale config json
 * @returns {Promise}
 */
export const fetchLocaleConfig = (lang: string) => (dispatch:AsyncAppDispatch, getState: () => RootState) => new Promise((resolve, reject) => {
  loading(true);
  const state = getState();
  const sdkLocales = state.core.programConfig?.brand?.sdkLocale.data;

  if (lang === 'en') {
    loading(false);
    resolve(state.core.ui.locale);
    return;
  }

  if (sdkLocales && Array.isArray(sdkLocales) && sdkLocales.length !== 0) {
    const language = sdkLocales.find((l) => l.language === lang);
    if (language) {
      /* */
      axios
        .get(language.fileUrl)
      // .then(() => {
        .then(({ data }) => {
          resolve(data);
        })
        .catch(() => {
          reject(new Error('INVALID_LANG'));
        })
        .finally(() => {
          loading(false);
        });
    } else {
      reject(new Error('INVALID_LANG'));
    }
  } else {
    reject(new Error('INVALID_LANG'));
  }

  /* */
  //
});

/**
 * Fetch program config data
 * @param {string} programId
 * @returns {Promise}
 */
export const fetchProgramConfig = (programId: string) => (dispatch:AsyncAppDispatch) => new Promise((resolve, reject) => {
  loading(true);
  http
    .get(`v1/program/${programId}`)
    .then(({ data }) => {
      dispatch(updateProgramConfig(data));
      resolve(data);
    })
    .catch((e) => {
      reject(e);
    });
  dispatch(updateProgramConfig({}));
  loading(false);
});
