import { personDetails } from '../types/user';
import { sendMessage } from './sdk';

/**
 * get error message from response
 * @param {*} error
 * @return {*}
 */
export const getErrorMessage = (error: any): any => {
  let msg = error;
  if (typeof error === 'string') {
    msg = error;
  } else if (error.response && error.response.data) {
    return getErrorMessage(error.response.data);
  } else if (error.errorDesc) {
    msg = error.errorDesc;
  } else if (error.message) {
    msg = error.message;
  }
  return msg;
};

export const showNotification = (title: string, description: string, status?: UseToastOptions['status']) => {
  const { toast } = createStandaloneToast();

  toast({
    title,
    position: 'top-right',
    description,
    status,
    duration: 9000,
    isClosable: true,
  });
};

/**
 * api error handler
 * @param {*} error
 * @returns {void}
 */
export const apiErrorHandler = (error: any): void => {
  if (error.status === 401 || error?.response?.status === 401) {
    sendMessage(COMMON.INVALID_AUTH_TOKEN, undefined, undefined, 'error');
  } else if (error.status === 500 || error?.response?.status === 500) {
    sendMessage(COMMON.UNKNOWN_ERROR, { error }, undefined, 'error');
  }

  const msg = getErrorMessage(error);

  showNotification('An error occurred.', typeof msg === 'string'
    ? msg
    : 'Something went wrong and we could not process your request.', 'error');
};

/**
 * loading state update
 * @param {boolean} isLoading
 * @returns {void}
 */
export const loading = (isLoading: boolean) => {
  store.dispatch(updateLoadingState(isLoading));
};

/**
 * find next screen
 * @param {personDetails['kyc']} kycDetails
 */
// eslint-disable-next-line no-async-promise-executor
export const findNextScreen = (kycDetails: personDetails['kyc']) => new Promise<string>(async (resolve, reject) => {
  try {
    const kycStatus = kycDetails.status;
    let nextScreen: string = '/ao/kyc/personalDetails';
    switch (kycStatus) {
      case 'notStarted':
        nextScreen = '/ao/kyc/personalDetails';
        break;
      case 'submitted':
        nextScreen = '/ao/kyc/review';
        break;
      case 'inReview':
        nextScreen = '/ao/kyc/review';
        break;
      case 'approved':
        // eslint-disable-next-line no-case-declarations
        const { total } = await store.dispatch((listAllAccounts as any)());
        if (total === 0) {
          nextScreen = '/ao/kyc/success';
        } else {
          nextScreen = '/ao/account/list';
        }
        break;
      case 'declined':
        nextScreen = '/ao/kyc/declined';
        break;
      default:
        nextScreen = '/';
    }
    resolve(nextScreen);
  } catch (e) {
    reject(e);
  }
});

/**
 * currency format
 * @param {number | string} value
 */
export const currencyFormat = (value: string | number, currency = 'USD'): string => {
  // Create our number formatter.
  const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency,

  // These options are needed to round to whole numbers if that's what you want.
  // minimumFractionDigits: 0, // (this suffices for whole numbers, but will print 2500.10 as $2,500.1)
  // maximumFractionDigits: 0, // (causes 2500.99 to be printed as $2,501)
  });

  return formatter.format(typeof value === 'string' ? parseFloat(value) : value);
};

/**
 * image preload
 * @param url
 * @param callback
 */
export function preloadImage(url: string, callback: null | (() => any) = null) {
  const img = new Image();
  img.src = url;
  img.onload = callback;
}

export function camel2title(camelCase: string) {
  // no side-effects
  return camelCase
    // inject space before the upper case letters
    .replace(/([A-Z])/g, (match) => ` ${match}`)
    // replace first char with upper case
    .replace(/^./, (match) => match.toUpperCase());
}

export function debounce<T extends Function>(cb: T, wait = 500) {
  let h: any = 0;
  const callable = (...args: any) => {
    clearTimeout(h);
    h = setTimeout(() => cb(...args), wait);
  };
  return <T>(<any>callable);
}

export default function appendStyle(id: string, css: string) {
  if (!document.head.querySelector(`#${id}`)) {
    const node = document.createElement('style');
    node.textContent = css;
    node.type = 'text/css';
    node.id = id;

    document.head.appendChild(node);
  }
}

export function isCrypto(a: account | cryptoAccount | null): a is cryptoAccount {
  return !!a && 'cryptoAddress' in a;
}

