/**
 * get vgs valut id
 * @return {string}
 */
export const getValutId = () => (store.getState().core.config.mode === 'live' ? process.env.REACT_APP_VGS_VALUTID_LIVE : process.env.REACT_APP_VGS_VALUTID_SANDBOX);

/**
 * Check component is lazy
 * @param object
 * @returns {boolean}
 */
export const isLazyComponent = (object: any): object is LazyExoticComponent<FC<unknown>> => object && '_init' in object;
