/* eslint-disable prefer-promise-reject-errors */
/* eslint-disable no-param-reassign */
import axios, { AxiosInstance } from 'axios';
import { COMMON } from '../const/errors';
import store from '../store';
import { updateUserAuthData } from '../store/actionCreators/user';
import { callParentMethod, sendMessage } from './sdk';

const coreData = store.getState().core.config;

const http: AxiosInstance = axios.create({
  baseURL: coreData.apiBasePath,
  // headers: {
  //   'SD-Device-ID': coreData.deviceId,
  // },
});

// let interceptorObj:number | null = null;

// eslint-disable-next-line func-names
export const createAxiosResponseInterceptor = () => {
  http.interceptors.response.use((response) => response, async (error) => {
    const originalRequest = error.config;
    if (error.response.status === 401 && !originalRequest._retry) {
      originalRequest._retry = true;
      try {
        const authToken: any = await callParentMethod('authTokenErrorHandler', []);
        if (typeof authToken === 'string') {
          store.dispatch(updateUserAuthData({
            accessToken: authToken,
          }));
          // Update http header
          http.defaults.headers.common.AUTHORIZATION = `Bearer ${authToken}`;
          error.config.headers.AUTHORIZATION = `Bearer ${authToken}`;
        }
        originalRequest._retry = false;
        return http(error.config);
      } catch (e) {
        sendMessage(COMMON.INVALID_AUTH_TOKEN, undefined, undefined, 'error');
        return Promise.reject(error);
      }
    } else {
      return Promise.reject(error);
    }
  });
};

export default http;
