import React, { useCallback, useEffect, useState } from 'react';
import {
  Button, Flex, Heading, Img, Text
} from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';
import style from './Welcome.module.css';
import MiniBox from '../layout/MiniBox';
import Logo from '../assets/images/logo.svg';
import { useAppAsyncDispatch } from '../hooks/store';
import {
  login, checkSession, getPersonFromAuth0, logout
} from '../store/middlewares/user';

function Welcome() {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const [person, updatePerson] = useState<any>();

  const getStarted = useCallback(() => {
    dispatch(login());
  }, []);

  useEffect(() => {
    dispatch(checkSession())
      .then(() => {
        dispatch(getPersonFromAuth0())
          .then((p) => {
            if (p) {
              updatePerson(p);
            }
          }).catch();
      }).catch(console.log);
  }, []);

  return (
    <div className="page">
      <MiniBox varient="box">
        <Flex direction="column" justify="space-between" width="100%">
          <div>
            <Img alt="logo" src={Logo} data-testid="logo" className={style.logo} />
          </div>
          {person ? (
            <>

              <Text textAlign="center" variant="primary" className={style.desc}>
                {`You already have an active session with ${person.name}, click continue to use same session, or click logout to use any other phone number`}
              </Text>
              <Flex style={{ width: '100%' }} gap={3}>
                <Button
                  flex={1}
                  role="link"
                  variant="primary"
                  onClick={() => {
                    dispatch(logout());
                  }}
                >
                  {t('welcome_screen.logout')}
                </Button>
                <Button flex={1} role="link" variant="primary" onClick={getStarted}>
                  {t('welcome_screen.continue')}
                </Button>
              </Flex>
            </>
          )
            : (
              <>
                <div>
                  <Heading variant="primary" textAlign="center">
                    {t('welcome_screen.title')}
                  </Heading>
                  <Text textAlign="center" variant="primary" className={style.desc}>
                    {t('welcome_screen.desc')}
                  </Text>
                </div>

                <Button role="link" variant="primary" onClick={getStarted}>
                  {t('welcome_screen.ctaText')}
                </Button>
              </>
            )}
        </Flex>

      </MiniBox>
    </div>
  );
}

export default Welcome;
