import { MOVE_FUNDS } from '../../const/errors';
import store from '../../store';
import BasePage, { pageInit } from '../BasePage';

export type FromCardData = {
  showBack?: boolean;
}

export default class FromCard extends BasePage<FromCardData> {
  protected url = '/pullfunds/from/card';

  protected defaultData:FromCardData = {
    showBack: true,
  };

  @pageInit()
  init() {
    return new Promise<string>((resolve, reject) => {
      const person = store.getState().user.personDetails;
      if (person?.address?.country.toUpperCase() === 'US') {
        resolve(this.url);
      } else {
        reject(new Error(MOVE_FUNDS.NOT_ALLOWED.TITLE));
      }
    });
  }
}
