import { AsyncAppAction, AsyncAppDispatch, RootState } from '..';
import {
  account, accountStatement, linkedCards, plaidTokenDetails, selfBankAccount
} from '../../types/account';
import {
  updateAccountDetails,
  updatePlaidToken,
  updateConnectedBankList,
  updateSelectedAccountDetails,
  updateLinkedCards,
  updateAccountStatements,
} from '../actionCreators/account';
import http from '../../utils/http';
import { loading } from '../../utils/general';
import { transaction } from '../../types/transaction';
import { setTransactionDetail } from '../actionCreators/transaction';

/**
 * list all accounts
 * @returns {Array<account>}
 */
export const listAllAccounts = (businessId: string | null = null, type = 'all', limit = 20, offset = 0): AsyncAppAction<{data: account[], total: number}> => (dispatch: AsyncAppDispatch) => new Promise((resolve, reject) => {
  loading(true);
  let url = `v1/account?limit=${limit}&offset=${offset}`;
  if (businessId) {
    url += `&businessId=${businessId}`;
  }
  if (type !== 'all') {
    url += `&type=${type}`;
  }

  http
    .get<{data: account[], total: number}>(url)
    .then(({ data }) => {
      const accounts: account[] = data.data.map((a1: account) => {
        const a = a1;
        a.availableBalance = a.availableBalance.length === 0 ? '0.00' : a.availableBalance;
        return a;
      });

      dispatch(updateAccountDetails({ accounts, total: data.total }));
      resolve({ data: accounts, total: data.total });
    })
    .catch((e) => {
      if (e.response && e.response.data) {
        reject(e.response.data);
      } else {
        reject(e);
      }
    })
    .finally(() => {
      loading(false);
    });
});

/**
 * get account details
 * @returns {Array<account>}
 */
export const getAccountDetails = (accountId: string): AsyncAppAction<account> => (dispatch: AsyncAppDispatch) => new Promise((resolve, reject) => {
  loading(true);
  const url = `v1/account/${accountId}`;

  http
    .get<account>(url)
    .then(({ data }) => {
      dispatch(updateSelectedAccountDetails(data));
      resolve(data);
    })
    .catch((e) => {
      if (e.response && e.response.data) {
        reject(e.response.data);
      } else {
        reject(e);
      }
    })
    .finally(() => {
      loading(false);
    });
});

/**
 * get account statements
 * @returns {Array<account>}
 */
export const fetchStatements = (accountId: string, limit = 20, offset = 0): AsyncAppAction<{
  total: number
  data: Array<accountStatement>
}> => (dispatch: AsyncAppDispatch, getState: () => RootState) => new Promise((resolve, reject) => {
  loading(true);

  http
    .get<{
      total: number
      data: Array<accountStatement>
    }>(`v1/account/${accountId}/statement?limit=${limit}&offset=${offset}`)
    .then(({ data }) => {
      const state = getState().account;
      let d: {
            total: number;
            data: accountStatement[];
          } = {
            total: 0,
            data: [],
          };
      if (offset === 0) {
        d = data;
      } else {
        const cards = { ...state.statements };
        d = {
          data: [...cards.data, ...data.data],
          total: data.total,
        };
      }

      dispatch(updateAccountStatements(d));
      resolve(d);
    })
    .catch((e) => {
      if (e.response && e.response.data) {
        reject(e.response.data);
      } else {
        reject(e);
      }
    })
    .finally(() => {
      loading(false);
    });
});

/**
 * get account statements
 * @returns {Array<account>}
 */
export const getStatementByMonth = (accountId: string, year: number, month: number, format = 'pdf'): AsyncAppAction<any> => () => new Promise((resolve, reject) => {
  loading(true);

  http
    .get<{
      total: number
      data: Array<any>
    }>(`v1/account/${accountId}/statement/${year}/${month}?export=${format}`, {
      responseType: format === 'pdf' ? 'blob' : 'json'
    })
    .then(({ data }) => {
      resolve(data);
    })
    .catch((e) => {
      if (e.response && e.response.data) {
        reject(e.response.data);
      } else {
        reject(e);
      }
    })
    .finally(() => {
      loading(false);
    });
});

/**
 * create account
 * @param {*} payload
 * @returns {account}
 */
export const createAccount = (payload: {
  type: string;
  label: string;
  createPhysicalCard: boolean;
  acceptedTerms: boolean;
  businessId?: string
}): AsyncAppAction<account> => (dispatch: AsyncAppDispatch) => new Promise((resolve, reject) => {
  loading(true);
  http
    .post('v1/account', payload)
    .then(({ data }) => {
      dispatch(listAllAccounts());

      resolve(data);
    })
    .catch((e) => {
      if (e.response && e.response.data) {
        reject(e.response.data);
      } else {
        reject(e);
      }
    }).finally(() => {
      loading(false);
    });
});

/**
 * list connected bank account
 * @param {string} accountId
 * @returns {Array<selfBankAccount>}
 */
export const listSelfBanks = (
  accountId: string,
): AsyncAppAction<selfBankAccount[]> => (dispatch: AsyncAppDispatch) => new Promise((resolve, reject) => {
  loading(true);
  http
    .get<{data: selfBankAccount[]}>(
      `/v1/contact?accountId=${accountId}&type=self`,
    )
    .then(({ data }) => {
      dispatch(updateConnectedBankList(data.data));
      resolve(data.data);
    })
    .catch((e) => {
      reject(e);
    })
    .finally(() => {
      loading(false);
    });
});

/**
 * list connected cards
 * @param {string} accountId
 * @returns {Array<selfBankAccount>}
 */
export const listSelfCards = (
  accountId: string,
  limit = 20,
  offset = 0
): AsyncAppAction<{
  data: linkedCards[];
  total: number;
}> => (dispatch: AsyncAppDispatch) => new Promise((resolve, reject) => {
  loading(true);
  http
    .get<{
      data: linkedCards[];
      total: number;
    }>(
      `v1/contact?accountId=${accountId}&type=others&limit=${limit}&offset=${offset}`,
    )
    .then(({ data }) => {
      const d = data.data.filter((c) => !!c.debitCard);
      const filteredData = {
        data: d,
        total: d.length
      };
      dispatch(updateLinkedCards(filteredData));
      resolve(data);
    })
    .catch((e) => {
      reject(e);
    })
    .finally(() => {
      loading(false);
    });
});

/**
 * delete connected bank account / cards
 * @param {string} contactId
 * @returns {*}
 */
export const deleteContact = (contactId: string) => () => new Promise((resolve, reject) => {
  loading(true);
  http
    .delete(`v1/contact/${contactId}`)
    .then(({ data }) => {
      resolve(data);
    })
    .catch((e) => {
      reject(e);
    })
    .finally(() => {
      loading(false);
    });
});

/**
 * delete connected bank account and fetch latest list
 * @param {string} contactId
 * @returns {*}
 */
export const removeSelfBank = (contactId: string, option: 'bank' | 'card' = 'bank') => (dispatch: AsyncAppDispatch, getState: () => RootState) => new Promise((resolve, reject) => {
  dispatch(deleteContact(contactId))
    .then((data) => {
      setTimeout(() => {
        const state = getState();
        const accountId = state.account.selectedAccount?.id;
        if (accountId) {
          if (option === 'bank') { dispatch(listSelfBanks(accountId)); } else { dispatch(listSelfCards(accountId)); }
        }
      }, 0);
      resolve(data);
    })
    .catch((e) => {
      reject(e);
    });
});

/**
 * fetch plaid token
 * @param {string} accountId
 * @returns {plaidTokenDetails}
 */
export const fetchPlaidToken = (
  accountId: string,
  updateModeParam?: {
    packageName: string
    plaidUpdateMode: {
      type: string
      contactId: string
    }
  }
): AsyncAppAction<plaidTokenDetails> => (dispatch: AsyncAppDispatch) => new Promise((resolve, reject) => {
  loading(true);
  http
    .post<plaidTokenDetails>(`/v1/account/${accountId}/plaid-token`, updateModeParam)
    .then(({ data }) => {
      dispatch(updatePlaidToken(data));
      resolve(data);
    })
    .catch((e) => {
      reject(e);
    })
    .finally(() => {
      loading(false);
    });
});

/**
 * link bank account to current account
 * @param {object} param
 * @property {string} param.plaidToken
 * @property {string} param.plaidAccountId
 * @property {string} param.accountId
 * @returns {*}
 */
export const linkPlaidAccount = ({
  plaidToken,
  plaidAccountId,
  accountId,
}: {
  plaidToken: string;
  plaidAccountId: string;
  accountId: string;
}) => (dispatch: AsyncAppDispatch) => new Promise((resolve, reject) => {
  loading(true);
  http
    .post(`/v1/account/${accountId}/plaid-account`, {
      plaidToken,
      plaidAccountId,
    })
    .then(({ data }) => {
      dispatch(listSelfBanks(accountId));

      resolve(data);
    })
    .catch((e) => {
      reject(e);
    })
    .finally(() => {
      loading(false);
    });
});

/**
 * ach receive (pull fund from bank-plaid)
 * @param {*} payload
 * @param {*} type
 * @returns {*}
 */
export const pullFund = (
  payload: {
    accountId: string;
    amount: string;
    contactId: string;
    description: string;
  },
  type: 'card' | 'bank' = 'bank',
): AsyncAppAction<transaction> => (dispatch) => new Promise((resolve, reject) => {
  loading(true);
  let url = '/v1/receive/ach';
  if (type === 'card') {
    url = '/v1/receive/debitpull';
  }

  http
    .post(url, payload)
    .then(({ data }) => {
      dispatch(setTransactionDetail(data));
      resolve(data);
    })
    .catch((e) => {
      reject(e);
    })
    .finally(() => {
      loading(false);
    });
});
