import BasePage, { pageInit } from '../BasePage';

export type TransactionDetailsData = {
    showBack?: boolean;
    amountFontWeight?: string
    amountFontSize?: string
    amountSentColor?: string
    amountReceivedColor?: string
    transactionId?: string | null;
    dateFormat?: string
}

export default class TransactionDetails extends BasePage<TransactionDetailsData> {
  protected url = `/transactions/details/${this.data.transactionId}`;

  protected defaultData:TransactionDetailsData = {
    showBack: true,
    transactionId: null,
    dateFormat: "MMM d, yyyy 'at' h:mm aa"
  };

  @pageInit()
  init() {
    return new Promise<string>((resolve, reject) => {
      if (this.data.transactionId && this.data.transactionId.length !== 0) {
        resolve(this.url);
      } else {
        reject(Error('INVALID_TRANSACTION'));
      }
    });
  }
}
