/* eslint-disable react/jsx-no-constructed-context-values */
import { Drawer, DrawerContent, DrawerOverlay } from '@chakra-ui/react';
import React, {
  PropsWithChildren, Suspense, useCallback, useRef, useState, createContext
} from 'react';
import Loader from '../components/Loader';
import { DrawerChildComponent, DrawerValue, HistoryData } from '../types/drawer';
import { isLazyComponent } from '../utils/general';

const initialValue : DrawerValue<unknown> = {
  isOpen: false,
  child: null,
  open: () => {},
  close: () => {},
  goBack: () => {},
};

export const DrawerContext = createContext<DrawerValue<unknown> | null>(null);

const DrawerProvider = ({ children }: PropsWithChildren) => {
  const [drawerValue, updateDrawerValue] = useState(initialValue);
  const drawerHistoryStack = useRef<HistoryData[]>([]);

  /**
   * Drawer open handler
   */
  const openHandler = useCallback((child: DrawerChildComponent, data: unknown) => {
    updateDrawerValue({
      ...drawerValue,
      isOpen: true,
      child,
      data,
    });

    drawerHistoryStack.current.push({
      component: child,
      data,
    });
  }, [drawerValue, drawerHistoryStack]);

  /**
   * Drawer close handler
   */
  const closeHandler = useCallback(() => {
    updateDrawerValue({
      ...drawerValue,
      isOpen: false,
      data: undefined,
    });
    drawerHistoryStack.current = [];
  }, [drawerValue]);

  /**
   * Drawer back handler
   */
  const goBack = useCallback(() => {
    drawerHistoryStack.current.pop();
    if (drawerHistoryStack.current.length !== 0) {
      const historyData = drawerHistoryStack.current[drawerHistoryStack.current.length - 1];
      updateDrawerValue({
        ...drawerValue,
        isOpen: true,
        child: historyData.component,
        data: historyData.data,
      });
    } else {
      closeHandler();
    }
  }, [drawerValue, drawerHistoryStack]);

  const ChildComponent = drawerValue.child;

  return (
    <DrawerContext.Provider value={{
      ...drawerValue,
      open: openHandler,
      close: closeHandler,
      goBack,
    }}
    >
      {children}

      <Drawer
        size="sm"
        isOpen={drawerValue.isOpen}
        onClose={() => {
          closeHandler();
        }}
      >
        <DrawerOverlay />
        <DrawerContent>
          <Suspense fallback={<Loader show />}>
            {isLazyComponent(ChildComponent) ? <ChildComponent /> : ChildComponent}
          </Suspense>
        </DrawerContent>
      </Drawer>
    </DrawerContext.Provider>
  );
};

export default DrawerProvider;
