import { applyMiddleware, createStore } from 'redux';
import thunk, { ThunkDispatch, ThunkAction } from 'redux-thunk';
import { rootReducer } from './reducers';

/**
 * Custom middle ware implementations
 */
// import { logger } from '../middlewares/logger';

const middlewares = [thunk];
// if (__DEV__) {
//   const logger = require('redux-logger');
//   const loggerMiddleware = logger.createLogger({
//     duration: true,
//   });
//   middlewares = [...middlewares, loggerMiddleware];
// }
const store = createStore(rootReducer, applyMiddleware(...middlewares));
/**
 * Add custom middlewares
 * They are executed in the order they are registered here
 */
// const store = createStore(reducers, applyMiddleware(...middlewares, logger));

export interface Action<T, P> {
  readonly type: T;
  readonly payload?: P;
}

// eslint-disable-next-line no-unused-vars
export type actionCreator<T> = (payload: T) => Action<string, T>;

export type RootState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;

export type AsyncAppDispatch = ThunkDispatch<
  RootState,
  any,
  Action<string, any>
>;

export type AsyncAppAction<T> = ThunkAction<
  Promise<T>,
  RootState,
  any,
  Action<string, any>
>;

export default store;
