import React from 'react';
import ReactDOM from 'react-dom/client';
import { Provider } from 'react-redux';
import FingerprintJS from '@fingerprintjs/fingerprintjs';
import store from './store/index';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { updateCoreData, updateUiOptions } from './store/actionCreators/core';
import localeInit from './locale';
import { initAuth0 } from './utils/auth0';

const themeConfig = require('./config.json');

type Params = {
  moduleName?: string;
  isDarkMode?: boolean
  showUI?: boolean,
  uniqueID?: string,
  /** UI Options */
  ui?: {
    loaderOverlayColor?: string
    loaderColor?: string
  }
}
const appInit = async ({
  isDarkMode = false, showUI = true, moduleName, uniqueID, ui
}: Params) => {
  const container = document.getElementById('root');
  const root = ReactDOM.createRoot(
    container as HTMLElement
  );

  await initAuth0();

  // We recommend to call `load` at application startup.
  const fp = await FingerprintJS.load();

  // The FingerprintJS agent is ready.
  // Get a visitor identifier when you'd like to.
  const result = await fp.get();

  // This is the visitor identifier:
  const { visitorId } = result;

  store.dispatch(updateCoreData({
    apiBasePath: process.env.REACT_APP_API_BASE || '',
    clientId: process.env.REACT_APP_CLIENT_ID || '',
    deviceId: visitorId,
    origin: window.location.origin,
    mode: process.env.REACT_APP_MODE === 'live' ? 'live' : 'sandbox',
    recaptchaToken: process.env.REACT_APP_RECAPTCHA_KEY,
    isDarkMode,
    moduleName,
    showUI,
    uniqueID,
    uiOptions: ui
  }));

  store.dispatch(updateUiOptions(themeConfig));

  localeInit({
    en: {
      translation: {
        ...themeConfig.locale
      }
    }
  });

  root.render(
    <div>
      <Provider store={store}>
        <App />
      </Provider>
    </div>
  );

  // If you want to start measuring performance in your app, pass a function
  // to log results (for example: reportWebVitals(console.log))
  // or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
  reportWebVitals();
};

appInit({
});
