import { AsyncAppAction, AsyncAppDispatch } from '..';
import http from '../../utils/http';
import { loading } from '../../utils/general';
import {
  buyQuote, cryptoAccount, sellQuote, sendCryptoGasFeesDetails, sendCryptoResponse, supportedCurrenciesResponse
} from '../../types/crypto';
import { filterData } from '../../types/transaction';
import {
  updateBuyQuoteData, updateCryptoAccountList, updateSelectedCryptoAccount, updateSellQuoteData, updateSendGasFeesData, updateSendResponseData, updateSupportedCurrencies
} from '../actionCreators/crypto';
/**
 * list all accounts
 * @returns {Array<account>}
 */
export const listAllCryptoAccounts = (businessId: string | null = null, type = 'all', limit = 20, offset = 0): AsyncAppAction<{data: cryptoAccount[], total: number}> => (dispatch: AsyncAppDispatch) => new Promise((resolve, reject) => {
  loading(true);
  let url = `v1/crypto?limit=${limit}&offset=${offset}`;
  if (businessId) {
    url += `&businessId=${businessId}`;
  }
  if (type !== 'all') {
    url += `&type=${type}`;
  }

  http
    .get<{data: cryptoAccount[], total: number}>(url)
    .then(({ data }) => {
      dispatch(updateCryptoAccountList(data));
      resolve(data);
    })
    .catch((e) => {
      if (e.response && e.response.data) {
        reject(e.response.data);
      } else {
        reject(e);
      }
    })
    .finally(() => {
      loading(false);
    });
});

