export const fetchCryptoTransactions = (accountId: string, limit = 25, offset = 0, query: string, filters?: filterData): AsyncAppAction<cryptoAccount> => () => new Promise((resolve, reject) => {
  loading(true);

  let url = `v1/crypto/${accountId}/transaction?limit=${limit}&offset=${offset}`;
  if (filters?.startDate) {
    url += `&startTime=${filters?.startDate}`;
  }
  if (filters?.endDate) {
    url += `&endTime=${filters?.endDate}`;
  }
  if (filters?.minAmount && filters?.minAmount !== '0.00' && filters?.minAmount !== '0') {
    url += `&minAmount=${filters?.minAmount}`;
  }
  if (filters?.maxAmount && filters?.maxAmount !== '0.00' && filters?.maxAmount !== '0') {
    url += `&maxAmount=${filters?.maxAmount}`;
  }
  if (query) {
    url += `&query=${query}`;
  }
  if (filters?.txnType) {
    url += `&txnType=${filters?.txnType}`;
  }

  http
    .get<cryptoAccount>(url)
    .then(({ data }) => {
      // let d = {};
      // if (offset === 0) {
      //   d = data;
      // } else {
      //   const transactions = { ...state.transactions };
      //   d = {
      //     data: [...transactions.data, ...data.data],
      //     total: data.total
      //   };
      // }
      resolve(data);
    })
    .catch((e) => {
      if (e.response && e.response.data) {
        reject(e.response.data);
      } else {
        reject(e);
      }
    })
    .finally(() => {
      loading(false);
    });
});

/**
 * get buy crypto quote
 * @param param0
 * @returns
 */
export const getBuyQuote = ({
  walletId, amount, cryptoCurrency, quantity
}: {
  walletId: string, cryptoCurrency: string, amount?: string, quantity?: string
}): AsyncAppAction<buyQuote> => (dispatch) => new Promise((resolve, reject) => {
  loading(true);

  let url = `v1/crypto/${walletId}/buy/quote?cryptoCurrency=${cryptoCurrency}`;

  if (amount) {
    url += `&amount=${amount}`;
  } else if (quantity) {
    url += `&quantity=${quantity}`;
  }

  http
    .get<buyQuote>(url)
    .then(({ data }) => {
      dispatch(updateBuyQuoteData(data));
      resolve(data);
    })
    .catch((e) => {
      if (e.response && e.response.data) {
        reject(e.response.data);
      } else {
        reject(e);
      }
    })
    .finally(() => {
      loading(false);
    });
});

