/* eslint-disable no-restricted-syntax */
/* eslint-disable class-methods-use-this */
import store, { AsyncAppDispatch } from '../store';
import { updateModuleData } from '../store/actionCreators/core';

export function pageInit() {
  return function (
    target: Object,
    key: string | symbol,
    descriptor: PropertyDescriptor
  ) {
    const childFunction = descriptor.value;

    // eslint-disable-next-line no-param-reassign
    descriptor.value = function (args: any[]) {
      /**
       * merging the default data into data
       */
      this.assignDefaultData();
      return childFunction.apply(this, args);
    };
    return descriptor;
  };
}

/**
 * Base module
 */
export default abstract class BasePage<T> {
  /**
   * default start screen path
   */
  protected abstract url: string;

  /**
   * default data
   */
  protected abstract defaultData: T;

  /**
   * redux dispatcher for dispatching the action
   */
  dispatch: AsyncAppDispatch = store.dispatch;

  /**
   * incomming data
   */
  protected data: T;

  /**
   * constructor
   * @param {*} data
   */
  constructor(data: T) {
    this.data = data;
  }

  /**
   * Set default data into data
   */
  protected assignDefaultData() {
    // default data assign
    this.data = { ...this.defaultData, ...this.data };
    const { moduleData } = store.getState().core;
    // store update
    this.dispatch(updateModuleData({
      ...moduleData,
      screenConfig: this.data
    }));
  }

  getDefaultData() {
    return this.defaultData;
  }

  /**
   * Init method to start the flow
   * @returns
   */
  @pageInit()
  init(): Promise<string> {
    return new Promise((resolve) => {
      resolve(this.url);
    });
  }
}
