import { Action } from '..';
import { contact } from '../../types/contact';
import { UPDATE_CONTACT_LIST_DATA, UPDATE_CONTACT_LOADING_STATE, UPDATE_SELECTED_CONTACT } from '../actionTypes/contact';

type contactStore = {
    loadingState: boolean,
    contacts: {
        data: contact[],
        total: number
    },
    selectedContact: contact | null
};

const initialData:contactStore = {
  loadingState: false,
  contacts: {
    data: [],
    total: 0
  },
  selectedContact: null
};

/**
 * contact reducer
 * @param {contactStore} state
 * @param {Action} action
 * @returns {contactStore}
 */
const contactReducer = (
  state: contactStore = initialData,
  action: Action<string, any>,
) => {
  const copiedState = { ...state };

  switch (action.type) {
    case UPDATE_CONTACT_LIST_DATA:
      copiedState.contacts = action.payload;
      return copiedState;
    case UPDATE_CONTACT_LOADING_STATE:
      copiedState.loadingState = action.payload;
      return copiedState;
    case UPDATE_SELECTED_CONTACT:
      copiedState.selectedContact = action.payload;
      return copiedState;
    default:
      return copiedState;
  }
};

export default contactReducer;
