import { getContactById } from '../../store/middlewares/contact';
import BasePage, { pageInit } from '../BasePage';

export type CheckDepositFormData = {
  contactId: string;
  showBack?: boolean;
}

export default class CheckDepositForm extends BasePage<CheckDepositFormData> {
  protected url = '/checkdeposit/form';

  protected defaultData = {
    contactId: '',
    showBack: true,
  };

  @pageInit()
  init() {
    return new Promise<string>((resolve, reject) => {
      if (this.data.contactId && this.data.contactId.length !== 0) {
        /**
         * contact validation
         */
        this.getContactById(this.data.contactId)
          .then(() => {
            resolve(this.url);
          }).catch(() => {
            reject(Error('INVALID_CONTACT_ID'));
          });
      } else {
        reject(Error('INVALID_CONTACT_ID'));
      }
    });
  }

  private getContactById(contactId: string) {
    return this.dispatch(getContactById(contactId));
  }
}
