import BasePage, { pageInit } from '../BasePage';

export type CardDetailsData = {
  cardId: string;
  showBack?: boolean;
}

export default class CardDetails extends BasePage<CardDetailsData> {
  protected url = `/cards/details/${this.data.cardId}`;

  protected defaultData = {
    cardId: '',
  };

  @pageInit()
  init() {
    return new Promise<string>((resolve, reject) => {
      if (this.data.cardId && this.data.cardId.length !== 0) {
        resolve(this.url);
      } else {
        reject(new Error('INVALID_CARD'));
      }
    });
  }
}
