import { Action } from '..';
import {
  UPDATE_ATM_LIST_DATA,
  UPDATE_ATM_LIST_LOADING,
  UPDATE_CARDS_DATA,
  UPDATE_SENT_CARDS_DATA,
  UPDATE_CARDS_LIST_LOADING_STATE,
  UPDATE_CARD_PAYLOAD_DATA,
  UPDATE_REPLACE_CARD_ID,
  UPDATE_SELECTED_CARD_DATA,
} from '../actionTypes/card';
import {
  atmData, card, cardPayload, sentCard
} from '../../types/card';

interface cardStore {
  cards: {
    data: card[];
    total: number;
  };
  sentCards: {
    data: sentCard[];
    total: number;
  };
  cardLoading: boolean;
  atmData: {
    data: atmData[];
    total: number;
  };
  cardPayload: cardPayload | null;
  atmListLoading: boolean;
  selectedCard: card | null;
  replaceCardId: string | null;
}

const initialData: cardStore = {
  cards: {
    data: [],
    total: 0,
  },
  sentCards: {
    data: [],
    total: 0,
  },
  cardLoading: true,
  atmData: {
    data: [],
    total: 0,
  },
  cardPayload: null,
  atmListLoading: true,
  selectedCard: null,
  replaceCardId: null
};
/**
 * Card reducer
 * @param {cardStore} state
 * @param {Action<string, any>} action
 * @returns {cardStore}
 */
const cardReducer = (
  state: cardStore = initialData,
  action: Action<string, any>,
) => {
  const copiedState = { ...state };
  switch (action.type) {
    case UPDATE_CARDS_DATA:
      copiedState.cards = action.payload;
      return copiedState;
    case UPDATE_SENT_CARDS_DATA:
      copiedState.sentCards = action.payload;
      return copiedState;
    case UPDATE_CARDS_LIST_LOADING_STATE:
      copiedState.cardLoading = action.payload;
      return copiedState;
    case UPDATE_ATM_LIST_DATA:
      copiedState.atmData = action.payload;
      return copiedState;
    case UPDATE_SELECTED_CARD_DATA:
      copiedState.selectedCard = action.payload;
      return copiedState;
    case UPDATE_ATM_LIST_LOADING:
      copiedState.atmListLoading = action.payload;
      return copiedState;
    case UPDATE_CARD_PAYLOAD_DATA:
      copiedState.cardPayload = action.payload;
      return copiedState;
    case UPDATE_REPLACE_CARD_ID:
      copiedState.replaceCardId = action.payload;
      return copiedState;
    default:
      return copiedState;
  }
};

export default cardReducer;
