      borderWidth: 0,
      shadowColor: '#14141400',
      shadowHorizontal: 0,
      shadowVertical: 0,
      shadowBlur: 0,
      shadowSpread: 0,
      textAlign: 'center',
      height: 48,
      paddingLeft: 15,
      paddingRight: 15,
      paddingTop: 15,
