import { actionCreator } from '..';
import {
  account,
  selfBankAccount,
  plaidTokenDetails,
  linkedCards,
  accountStatement,
} from '../../types/account';
import { AccountsByBusiness } from '../../types/business';
import {
  UPDATE_ACCOUNT_DETAILS,
  UPDATE_SELECTED_ACCOUNT_DETAILS,
  UPDATE_PLAID_TOKENS,
  UPDATE_SELF_BANK_LIST,
  FUND_SELECTED_ACCOUNT_ID,
  UPDATE_LINKED_CARDS,
  FUND_SELECTED_AMOUNT,
  UPDATE_ACCOUNTS_BY_BUSINESS_DATA,
  UPDATE_PULL_FUND_DATA,
  UPDATE_ACCOUNT_STATEMENTS,
  UPDATE_SELECTED_ACCOUNT_TYPE,
  UPDATE_SETUP_WALLET_OPTIONS
} from '../actionTypes/account';

/**
 * update current account details
 * @param {Array<account>} payload
 * @returns {*}
 */
export const updateAccountDetails: actionCreator<{accounts: account[], total: number}> = (
  payload: {accounts: account[], total: number},
) => ({
  type: UPDATE_ACCOUNT_DETAILS,
  payload,
});

/**
 * update current account details
 * @param {Array<account>} payload
 * @returns {*}
 */
export const updateAccountsByBusiness: actionCreator<{
  [id: string]: AccountsByBusiness
}> = (
  payload: {
    [id: string]: AccountsByBusiness
  },
) => ({
  type: UPDATE_ACCOUNTS_BY_BUSINESS_DATA,
  payload,
});

/**
 * update selected account details
 * @param {account} payload
 * @returns {*}
 */
export const updateSelectedAccountDetails: actionCreator<account> = (
  payload: account,
) => ({
  type: UPDATE_SELECTED_ACCOUNT_DETAILS,
  payload,
});

/**
 * update current account details
 * @param {Array<account>} payload
 * @returns {*}
 */
export const updateSelectedAccountType: actionCreator<'cash' | 'crypto'> = (
  payload,
) => ({
  type: UPDATE_SELECTED_ACCOUNT_TYPE,
  payload,
});

/**
 * update connected bank list by plaid
 * @param {selfBankAccount[]} payload
 * @returns {*}
 */
export const updateConnectedBankList: actionCreator<selfBankAccount[]> = (
  payload: selfBankAccount[],
) => ({
  type: UPDATE_SELF_BANK_LIST,
  payload,
});

/**
 * update plaid token
 * @param {plaidTokenDetails} payload
 * @returns {*}
 */
export const updatePlaidToken: actionCreator<plaidTokenDetails> = (
  payload: plaidTokenDetails,
) => ({
  type: UPDATE_PLAID_TOKENS,
  payload,
});

/**
 * update selected account id
 * @param {string} payload
 * @returns {*}
 */
export const updateFundSelectedAccountId: actionCreator<string> = (
  payload: string,
) => ({
  type: FUND_SELECTED_ACCOUNT_ID,
  payload,
});

/**
 * update selected amount
 * @param {string} payload
 * @returns {*}
 */
export const updateFundSelecteAmount: actionCreator<string> = (
  payload: string,
) => ({
  type: FUND_SELECTED_AMOUNT,
  payload,
});

/**
 * update linked cards list
 * @param {linkedCards} payload
 * @returns {*}
 */
export const updateLinkedCards: actionCreator<{
  data: linkedCards[];
  total: number;
}> = (payload) => ({
  type: UPDATE_LINKED_CARDS,
  payload,
});

/**
 * update pull fund data
 * @param {linkedCards} payload
 * @returns {*}
 */
export const updatePullFundData: actionCreator<{
  label: string;
  amount: string;
  description: string;
} | null> = (payload) => ({
  type: UPDATE_PULL_FUND_DATA,
  payload,
});

/**
 * update account statements
 * @param {linkedCards} payload
 * @returns {*}
 */
export const updateAccountStatements: actionCreator<{
  data: accountStatement[];
  total: number;
}> = (payload) => ({
  type: UPDATE_ACCOUNT_STATEMENTS,
  payload,
});

export const updateSetupWalletOptions: actionCreator<{
  accountType: 'cash' | 'card' | 'crypto',
  businessId: string | null
}> = (payload) => ({
  type: UPDATE_SETUP_WALLET_OPTIONS,
  payload
});
