import { User } from '@auth0/auth0-spa-js';
import { AsyncAppAction, AsyncAppDispatch } from '..';
import { authData, idvDetails, personDetails } from '../../types/user';
import { getAuth0Client } from '../../utils/auth0';
import { loading } from '../../utils/general';
import http from '../../utils/http';
import {
  updateKycDetails,
  updatePersonDetails,
  updateUserAuthData,
} from '../actionCreators/user';

/**
 * login api call
 * @param {string} mobileNumber
 * @returns {Promise}
 */
export const login = () => () => new Promise((resolve, reject) => {
  loading(true);
  const auth0 = getAuth0Client();
  auth0.loginWithRedirect({
    authorizationParams: {
      redirect_uri: `${window.location.origin}/login-callback`,
      responseType: 'token id_token',
      scope: 'openid profile phone offline_access',
      audience: process.env.REACT_APP_AUTH0_AUDIENCE,
      logoUrl: `${window.location.origin}/solid/logos/Logo.svg`,
      supportedCountries: ['US', 'IN'],
      primaryColor: '#141414',
      defaultCountry: 'US',
    }
  }).then(() => {
    // logged in. you can get the user profile like this:
    auth0.getUser().then((user) => {
      resolve(user);
    });
  }).catch(reject)
    .finally(() => {
      loading(false);
    });
});

/**
 * Fetch current logged in person details
 *
 * @see {@link https://www.solidfi.com/docs/retrieve-a-person}
 * @returns {Promise<personDetails>}
 */
export const getPerson = (showLoader = true): AsyncAppAction<personDetails> => (dispatch: AsyncAppDispatch) => new Promise((resolve, reject) => {
  if (showLoader) {
    loading(true);
  }

  http
    .get<personDetails>('v1/person')
    .then(({ data }) => {
      dispatch(updatePersonDetails(data));
      resolve(data);
    })
    .catch((e) => {
      if (e.response && e.response.data) {
        reject(e.response.data);
      } else {
        reject(e);
      }
    })
    .finally(() => {
      if (showLoader) { loading(false); }
    });
});

/**
 * verify auth0 callback
 * @returns {Promise<void>}
 */
export const verifyAuth = () => () => new Promise((resolve, reject) => {
  loading(true);

  getAuth0Client().handleRedirectCallback()
    .then(() => resolve(true))
    .catch(reject)
    .finally(() => {
      loading(false);
    });
});

/**
 * verify auth0 callback
 * @returns {Promise<void>}
 */
export const checkSession = () : AsyncAppAction<authData> => (dispatch: AsyncAppDispatch) => new Promise((resolve, reject) => {
  loading(true);

  getAuth0Client().getTokenSilently({
    cacheMode: 'off',
    authorizationParams: {
      redirect_uri: `${window.location.origin}/login-callback`,
      responseType: 'token id_token',
      scope: 'openid profile phone offline_access',
      audience: process.env.REACT_APP_AUTH0_AUDIENCE,
    },
  })
    .then((accessToken) => {
      http.defaults.headers.common.AUTHORIZATION = `Bearer ${accessToken}`;
      dispatch(updateUserAuthData({ accessToken }));
      resolve({ accessToken });
    })
    .catch(reject)
    .finally(() => {
      loading(false);
    });
});

/**
 * Register the user
 * @returns {Promise<personDetails>}
 */
export const register = (params: {idNumberLast4?: string, accessToken: string}): AsyncAppAction<authData & {refreshRequired?: boolean}> => (dispatch: AsyncAppDispatch) => new Promise((resolve, reject) => {
  loading(true);

  http
    .post<authData>('v1/auth/register', {
      clientId: process.env.REACT_APP_AUTH0_CLIENT_ID,
      idNumberLast4: params.idNumberLast4,
    }, {
      headers: {
        AUTHORIZATION: `Bearer ${params.accessToken}`
      }
    })
    .then(({ data }) => {
      if ('accessToken' in data) {
        http.defaults.headers.common.AUTHORIZATION = `Bearer ${data.accessToken}`;
      }
      dispatch(updateUserAuthData(data));
      resolve(data);
    })
    .catch((e) => {
      if (e.response && e.response.data) {
        reject(e.response.data);
      } else {
        reject(e);
      }
    })
    .finally(() => {
      loading(false);
    });
});

/**
 * get person from auth0
 */
export const getPersonFromAuth0 = () : AsyncAppAction<User | undefined> => () => new Promise((resolve, reject) => {
  loading(true);
  getAuth0Client().getUser().then((user) => {
    resolve(user);
  }).catch(reject)
    .finally(() => {
      loading(false);
    });
});

/**
 * get person from auth0
 */
export const logout = () : AsyncAppAction<void> => () => new Promise((resolve, reject) => {
  loading(true);
  getAuth0Client().logout().then(() => {
    resolve();
  }).catch(reject)
    .finally(() => {
      loading(false);
    });
});

/**
 * Update person details
 *
 * @see {@link https://www.solidfi.com/docs/update-a-person}
 * @param {string} personId
 * @param {*} person
 * @returns {Promise<personDetails>}
 */
export const updatePerson = (
  personId: string,
  person: any,
): AsyncAppAction<personDetails> => (dispatch: AsyncAppDispatch) => new Promise((resolve, reject) => {
  loading(true);
  http
    .patch<personDetails>(`v1/person/${personId}`, person)
    .then(({ data }) => {
      dispatch(updatePersonDetails(data));
      resolve(data);
    })
    .catch((e) => {
      if (e.response && e.response.data) {
        reject(e.response.data);
      } else {
        reject(e);
      }
    })
    .finally(() => {
      loading(false);
    });
});

/**
 * submit kyc
 * @param {string} personId
 * @returns {Promise<personDetails.kyc>}
 */
export const submitKyc = (
  personId: string,
): AsyncAppAction<personDetails['kyc']> => (dispatch: AsyncAppDispatch) => new Promise((resolve, reject) => {
  loading(true);
  http
    .post<personDetails['kyc']>(`v1/person/${personId}/kyc`, {
      personId,
    })
    .then((res) => {
      /** @type {import('./state').kycState['kycData']} */
      const { data } = res;
      dispatch(updateKycDetails(data));
      resolve(data);
    })
    .catch((e) => {
      if (e.response && e.response.data) {
        reject(e.response.data);
      } else {
        reject(e);
      }
    })
    .finally(() => {
      loading(false);
    });
});

