import { useCallback } from 'react';
import { shallowEqual } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { updateCoreData, updateModuleData, updateModuleLoadingState } from '../store/actionCreators/core';
import { updateUserAuthData } from '../store/actionCreators/user';
import appendStyle, { debounce } from '../utils/general';
import http, { createAxiosResponseInterceptor } from '../utils/http';
import { sendMessage } from '../utils/sdk';
import { useAppAsyncDispatch, useAppSelector } from './store';
import {
  Cards, CheckDeposit,
  Contacts,
  Crypto,
  Kyb,
  Kyc,
  Movefunds,
  Payments,
  SetupWallet,
  Transactions,
  WalletList,
  Dashboard
} from '../modules';
import BaseModule from '../modules/BaseModule';
import { COMMON } from '../const/messages';
import { COMMON as COMMON_ERROR } from '../const/errors';

const moduleMap: Record<string, any> = {
  cards: Cards,
  checkDeposit: CheckDeposit,
  contacts: Contacts,
  crypto: Crypto,
  kyb: Kyb,
  kyc: Kyc,
  moveFunds: Movefunds,
  payments: Payments,
  setupWallet: SetupWallet,
  transactions: Transactions,
  walletList: WalletList,
  dashboard: Dashboard,
};

let activeModule: BaseModule<any> | null = null;

/**
 * module navigation
 */
export const useModuleNavigation = () => {
  const navigate = useNavigate();
  const { ui: appConfig, config: coreConfig } = useAppSelector((store) => store.core, shallowEqual);
  const dispatch = useAppAsyncDispatch();

  return useCallback((moduleName: string, moduleData: any, uID?: string | number, navigateToModule = true) => new Promise((resolve, reject) => {
    const data = typeof moduleData === 'object' ? { ...moduleData } : {};
    /**
     * module permission check
     */
    // if (!Object.keys(appConfig.modules).includes(moduleName)) {
    //   throwError('NOT_ALLOWED', uID);
    //   return;
    // }

    /**
     * adaptive height check
     */
    if (data.adaptiveHeight) {
      const rootElement = document.getElementById('root');
      const cb = debounce(() => {
        sendMessage('heightChange', { height: rootElement?.clientHeight || 0 });
      }, 300);
      const observer = new MutationObserver((mutations) => {
        mutations.forEach(() => {
          cb();
        });
      });

      const observerConfig = {
        subtree: true,
        childList: true,
        characterData: true
      };
      if (rootElement) {
        observer.observe(rootElement, observerConfig);
      }
    }

    /**
     * custom css
     */
    if (data.css) {
      appendStyle(`${moduleName}-custom-css`, data.css);
    }

    /**
     * auth token check
     */
    if (data?.authToken) {
      dispatch(updateUserAuthData({
        accessToken: data.authToken,
      }));
      // Update http header
      http.defaults.headers.common.AUTHORIZATION = `Bearer ${data.authToken}`;
      createAxiosResponseInterceptor();

      setTimeout(async () => {
        if (moduleName in moduleMap) {
          // core data
          if (data.isDarkMode !== undefined) {
            dispatch(updateCoreData({
              ...coreConfig,
              isDarkMode: data.isDarkMode
            }));
          }

          dispatch(updateModuleLoadingState(true));
          const module: BaseModule<any> = new moduleMap[moduleName](data, () => {
            dispatch(updateModuleData(data));
            module.init()
              .then((url) => {
                setTimeout(() => {
                  document.body.setAttribute('data-module', moduleName);
                  activeModule = module;
                  if (navigateToModule) {
                    navigate(url);
                    sendMessage(COMMON.MODULE_OPEN_SUCCESS, {
                      moduleName
                    }, uID);
                  }
                  resolve('MODULE_OPEN_SUCCESS');
                }, 0);
              }).catch((e) => {
                sendMessage(COMMON_ERROR.UNKNOWN_ERROR, { error: e }, uID, 'error');
                reject(Error(e));
              })
              .finally(() => {
                dispatch(updateModuleLoadingState(false));
              });
          });
        } else {
          sendMessage(COMMON_ERROR.INVALID_MODULE, undefined, uID, 'error');
          reject(Error('INVALID_MODULE'));
        }
      }, 0);
    } else {
      sendMessage(COMMON_ERROR.INVALID_AUTH_TOKEN, undefined, uID, 'error');
      reject(Error('INVALID_AUTH_TOKEN'));
    }
  }), [appConfig, coreConfig]);
};

/**
 * module method call
 * @returns {*}
 */
export const useModuleInit = () => {
  const appConfig = useAppSelector((store) => store.core.ui, shallowEqual);
  const dispatch = useAppAsyncDispatch();

  return useCallback((moduleName: string, moduleData: any) => new Promise((resolve) => {
    const data = typeof moduleData === 'object' ? { ...moduleData } : {};
    if (data?.authToken) {
      dispatch(updateUserAuthData({
        accessToken: data.authToken,
      }));
      // Update http header
      http.defaults.headers.common.AUTHORIZATION = `Bearer ${data.authToken}`;
      createAxiosResponseInterceptor();
    }
    if (moduleName in moduleMap) {
      const module: BaseModule<any> = new moduleMap[moduleName](data);
      resolve(module);
    }
  }), [appConfig]);
};

export function useModuleData<T = any>(): T & {adaptiveHeight: boolean} {
  const moduleData = useAppSelector((store) => store.core.moduleData, shallowEqual);
  return moduleData || {};
}

export function useModulePageData<T = any, S extends string | undefined = string>(pageName: S): Required<T> {
  const moduleData = useAppSelector((store) => store.core.moduleData, shallowEqual);
  let defaultPageParams:any = {};
  if (pageName) {
    const defaultParams = activeModule?.getDefaultParams();
    const startScreen = moduleData?.startScreen || defaultParams?.startScreen;
    if (startScreen && startScreen !== pageName) {
      defaultPageParams = activeModule?.getPageDefaultParams(pageName);
      return defaultPageParams || {};
    }
  }
  const screenConfig = moduleData.screenConfig || {};
  return { ...defaultPageParams, ...screenConfig };
}

export const useModuleConfig = (moduleName: string) => {
  const moduleData = useAppSelector((store) => store.core.ui.modules, shallowEqual);

  return moduleData[moduleName] ?? {};
};
