import { Action } from '..';
import {
  account,
  accountStatement,
  linkedCards,
  plaidTokenDetails,
  selfBankAccount,
} from '../../types/account';
import { AccountsByBusiness } from '../../types/business';
import {
  FUND_SELECTED_ACCOUNT_ID,
  UPDATE_ACCOUNT_DETAILS,
  UPDATE_LINKED_CARDS,
  UPDATE_PLAID_TOKENS,
  UPDATE_SELF_BANK_LIST,
  FUND_SELECTED_AMOUNT,
  UPDATE_SELECTED_ACCOUNT_DETAILS,
  UPDATE_ACCOUNTS_BY_BUSINESS_DATA,
  UPDATE_PULL_FUND_DATA,
  UPDATE_ACCOUNT_STATEMENTS,
  UPDATE_SELECTED_ACCOUNT_TYPE,
  UPDATE_SETUP_WALLET_OPTIONS
} from '../actionTypes/account';

interface accountStore {
  accounts: account[];
  totalAccounts: number;
  accountsByBusiness: {
    [id: string]: AccountsByBusiness
  }
  selectedAccount: account | null;
  selectedAccountType: 'cash' | 'crypto';
  setupWalletOptions: {
    accountType: 'cash' | 'card' | 'crypto',
    businessId: string | null
  }
  selfBankAccounts: selfBankAccount[];
  plaidTokens: plaidTokenDetails | null;
  fundSelectedAccountId: string | null;
  fundSelectedAmount: string;
  linkedCards: {
    data: linkedCards[];
    total: number;
  };
  pullFundData: {
    label: string;
    amount: string;
    description: string;
  } | null;
  statements: {
    total: number
    data: Array<accountStatement>
  }
}

const initialData: accountStore = {
  accounts: [],
  totalAccounts: 0,
  accountsByBusiness: {},
  selectedAccount: null,
  selectedAccountType: 'cash',
  setupWalletOptions: {
    accountType: 'cash',
    businessId: null
  },
  selfBankAccounts: [],
  plaidTokens: null,
  fundSelectedAccountId: null,
  fundSelectedAmount: '',
  linkedCards: {
    data: [],
    total: 0,
  },
  pullFundData: null,
  statements: {
    total: 0,
    data: []
  }
};

/**
 * account reducer
 * @param {accountStore} state
 * @param {Action} action
 * @returns {accountStore}
 */
const accountReducer = (
  state: accountStore = initialData,
  action: Action<string, any>,
) => {
  const copiedState = { ...state };

  switch (action.type) {
    case UPDATE_ACCOUNT_DETAILS:
      copiedState.accounts = action.payload.accounts;
      copiedState.totalAccounts = action.payload.total;
      return copiedState;
    case UPDATE_SELECTED_ACCOUNT_DETAILS:
      copiedState.selectedAccount = action.payload;
      return copiedState;
    case UPDATE_SELECTED_ACCOUNT_TYPE:
      copiedState.selectedAccountType = action.payload;
      return copiedState;
    case UPDATE_SELF_BANK_LIST:
      copiedState.selfBankAccounts = action.payload;
      return copiedState;
    case UPDATE_PLAID_TOKENS:
      copiedState.plaidTokens = action.payload;
      return copiedState;
    case FUND_SELECTED_ACCOUNT_ID:
      copiedState.fundSelectedAccountId = action.payload;
      return copiedState;
    case FUND_SELECTED_AMOUNT:
      copiedState.fundSelectedAmount = action.payload;
      return copiedState;
    case UPDATE_LINKED_CARDS:
      copiedState.linkedCards = action.payload;
      return copiedState;
    case UPDATE_ACCOUNTS_BY_BUSINESS_DATA:
      copiedState.accountsByBusiness = action.payload;
      return copiedState;
    case UPDATE_PULL_FUND_DATA:
      copiedState.pullFundData = action.payload;
      return copiedState;
    case UPDATE_ACCOUNT_STATEMENTS:
      copiedState.statements = action.payload;
      return copiedState;
    case UPDATE_SETUP_WALLET_OPTIONS:
      copiedState.setupWalletOptions = action.payload;
      return copiedState;
    default:
      return copiedState;
  }
};

export default accountReducer;
